import React from "react";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
/**
 * MapTypes component is called from maps.js and it helps 
 * loading different types of maps on Maps through call backs
 */
class MapTypes extends React.Component {
  render() {
    return (
      <Tabs
        defaultActiveKey="swell"
        className="nav nav-tabs justify-content-center"
        onSelect={(key) => this.props.onSubmit(key)}
      >
        <Tab eventKey="swell" title="Swell"></Tab>
        <Tab eventKey="wind" title="Wind"></Tab>
        <Tab eventKey="period" title="Period"></Tab>
      </Tabs>
    );
  }
}

export default MapTypes;
