import React, { Component } from "react";
import { Row } from "react-bootstrap";
import SwellDay from "./SwellDay";
/**
 * SwellDays acts as a container for each SwellDay.
 * SwellDays takes every day of data in props, and
 * maps each day to a SwellDay Component
 */
export default class SwellDays extends Component {
  render() {
    return (
      <Row>
        {this.props.sData.map((data) => (
          <SwellDay sData={data} />
        ))}
      </Row>
    );
  }
}
