import React from "react";
import Button from "react-bootstrap/Button";
import { useAuth0 } from "@auth0/auth0-react";
/**
 * This component creates a Logout button that will trigger auth0's logout hook
 * @returns Button
 */
const LogoutButton = () => {
  const { logout } = useAuth0();

  return (
    <Button
      style={{ margin: "px 10px 0px 0px" }}
      variant="outline-light"
      onClick={() => logout()}
    >
      Log Out
    </Button>
  );
};

export default LogoutButton;
