import React, { Component } from "react";
import SwellChart from "./SwellChart/SwellChart";
import WindComponent from "./WindCharts/WindComponent";
import WeatherSpecs from "./WeatherSpecs";
import Weather from "./WeatherChart/Weather";
import history from "../../history";
import Questionnaire from "../Questionnaire/Questionnaire";
/**
 * SpotForecast acts as a container for every SwellChart.js
 * WindComponent.js, Weather.js, and WeatherSpecs.js
 */
export class SpotForecast extends Component {
  /**
   * onQuestionnaireClick prevents the page to reload
   * when it should not
   * @param {*} e 
   */
  onQuestionnaireClick = (e) => {
    e.preventDefault();
    history.push("/questionnaire");
  };
  render() {
    return (
      <div>
        <div style={{ textAlign: "center" }}>
          {console.log(this.props)}
          <WeatherSpecs data={this.props} />
          <div style={{ paddingLeft: "15px", textAlign: "left" }}>
            <p>
              To get gear recommendations,{" "}
              <a
                onClick={(e) => this.onQuestionnaireClick(e)}
                href="/questionnaire"
              >
                click here
              </a>
            </p>
          </div>
          <SwellChart data={this.props.data} />
          <WindComponent data={this.props.data} />
          <Weather data={this.props.data} />
        </div>
      </div>
    );
  }
}

export default SpotForecast;
