import React from "react";
import Card from "react-bootstrap/Card";
import ListGroup from "react-bootstrap/ListGroup";

/**
 * WeatherSpecs acts as a container storing a
 * summary of the forecast for a surfspot
 */
class WeatherSpecs extends React.Component {
  weatherSpec = this.props.spec;
  render() {
    return (
      <Card border="primary" style={{ width: "15rem", margin: "10px" }}>
        <Card.Header>{this.props.spec}</Card.Header>
        <Card.Body>
          <Card.Title>{this.props.description}</Card.Title>
        </Card.Body>
      </Card>
    );
  }
}

export default WeatherSpecs;
