import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import WetSuitResults from "./WetSuitResults";
import SurfBoardResults from "./SurfBoardResults";
/**
 * Questionnaire loads the user's questionnaire results
 * (if they exist) in their profile.
 * Questionnaire contains the components
 * SurfBoardResults.js and WetsuitResults.js
 */
export class Questionnaire extends Component {
  render() {
    return (
      <Container fluid>
        <Row>
          <Col>
            <WetSuitResults />
          </Col>
          <Col>
            <SurfBoardResults />
          </Col>
        </Row>
      </Container>
    );
  }
}

export default Questionnaire;
