import React from "react";
import { Container } from "react-bootstrap";
import wsl from "./wsl.png";
/**
 * InformationFooter is a component that acts
 * as a footer and serves to give credit to other
 * entities that helped the project
 */
class InformationFooter extends React.Component {
  render() {
    return (
      <body className="d-flex flex-column" style={{ paddingBottom: "150px" }}>
        <footer className="footer mt-auto bg-primary py-0 text-white fixed-bottom">
          <Container>
            <div
              style={{
                display: "inline-block",
                width: "100%",
                marginBottom: "-9px",
              }}
            >
              <div style={{ float: "left" }}>
                <p style={{ margin: "10px 0" }}>
                  SurfScope Forecast <span>&copy; 2020</span>
                </p>
              </div>
              <div style={{ float: "right" }}>
                <a
                  style={{ marginRight: "10px" }}
                  href="https://www.worldsurfleague.com/"
                >
                  <img style={{ width: "158px", height: "40px" }} src={wsl} />
                </a>
                <a href="http://magicseaweed.com">
                  <img src="https://im-1-uk.msw.ms/msw_powered_by.png" />
                </a>
              </div>
            </div>
          </Container>
        </footer>
      </body>
    );
  }
}

export default InformationFooter;
