import React from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Badge from "react-bootstrap/Badge";
import "./PostCategory.css";
import { Button } from "react-bootstrap";
import draftToHtml from "draftjs-to-html";
import parse from "html-react-parser";
import axios from "axios";
import moment from "moment";
import * as urls from "../../../urls";

export default class Post extends React.Component {
  contentState = JSON.parse(this.props.post.content);
  contentHTML = draftToHtml(this.contentState);
  reactElement = parse(this.contentHTML);

  OnDeletePost = () => {
    axios
      .delete(`${urls.BACKEND_URL}/api/post/${this.props.post.id}/delete/`)
      .then((postResponse) => {
        console.log(postResponse.data);
        this.props.getPosts(postResponse.data, false);
      });
  };

  render() {
    let newDate = new Date(this.props.post.created_at);
    return (
      <div className="topic-container">
        <Row>
          <Col sm={8}>
            <Badge variant="primary">{this.props.post.username}</Badge>
            <span> - {moment(newDate).format("lll")}</span>
          </Col>
        </Row>
        <Row>
          <Col>{this.reactElement}</Col>
          <Col sm={4}>
            <div className="topic-button">
              {this.props.userID === this.props.post.creator_id && (
                <Button
                  className="forum-buttom "
                  variant="danger"
                  onClick={this.OnDeletePost}
                >
                  Delete
                </Button>
              )}
            </div>
          </Col>
        </Row>
      </div>
    );
  }
}
