import React from "react";
import { Card, Form, Button } from "react-bootstrap";
import axios from "axios";
import { withAuth0 } from "@auth0/auth0-react";
import createSurfChartObject from "./SurfChartObject";
import * as urls from "../../urls";
/**
 * SurfBoardQuestionnaire contains the questionnarie's
 * form regarding the surfboard part
 */
class SurfBoardQuestionnaire extends React.Component {
  // the state contain all the values that need to be
  // shared with other components. Some variables
  // have a default value that matches the dropdowns 1st value
  state = {
    weight: "",
    feet: "",
    inches: "",
    surfboardSize: "longboard",
    expLevel: "SUPER BEGINNER",
    waveHeight: "2 to 3 ft",
  };
  /**
   * handleWeightChange uses the table included in
   * SurfChartObject to determine an appropiate volume.
   * For this reason, handleWeightChange rounds the values up
   * so the result can match a key in the table
   * @param {*} e
   */
  handleWeightChange = (e) => {
    let chosenWeight;
    chosenWeight = parseInt(e.target.value);
    chosenWeight += 5 - (chosenWeight % 5);
    this.setState({ weight: chosenWeight });
  };
  /**
   * handlefeetChange stores the form option chosen
   * by the user in the state for the height (feet) option
   * @param {*} e
   */
  handlefeetChange = (e) => {
    this.setState({ feet: e.target.value });
  };
  /**
   * handleInchesChange stores the form option chosen
   * by the user in the state for the height (inches) option
   * @param {*} e
   */
  handleInchesChange = (e) => {
    this.setState({ inches: e.target.value });
  };
  /**
   * handleSurfboardSizeChange stores the form option chosen
   * by the user in the state for the  surfboard size option
   * @param {*} e
   */
  handleSurfboardSizeChange = (e) => {
    this.setState({ surfboardSize: e.target.value });
  };
  /**
   * handleExpLevelChange stores the form option chosen
   * by the user in the state for the experience level option
   * @param {*} e
   */
  handleExpLevelChange = (e) => {
    this.setState({ expLevel: e.target.value });
  };
  /**
   * hanfleWaveHeightChange stores the form option chosen
   * by the user in the state for the wave height option
   * @param {*} e
   */
  handleWaveHeightChange = (e) => {
    this.setState({ waveHeight: e.target.value });
  };
  /**
   * generateSurfboardUrl creates a urls that redirects to surfboards based on the
   * weight and the experience level of the user
   * For the level "super beginner" an intro board is recommended
   * regardless the inputs
   * the 3.5 value is chosen to create a range of volumes
   * @returns
   */
  generateSurfboardUrl = () => {
    let table = createSurfChartObject();
    let url = "";
    if (this.state.expLevel == "SUPER BEGINNER")
      return "https://www.amazon.com/s?k=foam+board+wavestorm&i=sporting&rh=n%3A3375251%2Cp_36%3A1253559011&dc&qid=1618511057&rnid=386589011&ref=sr_nr_p_36_4";

    let volume = table[this.state.weight][this.state.expLevel];

    url = `https://www.firewiresurfboards.com/collections/shortboards?_=pf&pf_vrs1_volume=${Number.parseFloat(
      volume - 3.5
    ).toPrecision(4)}:${Number.parseFloat(volume + 3.5).toPrecision(4)}`;
    return url;
  };
  /**
   * onSubmitSurfBoard sends the callback containing the results
   * to props. If the user is logged in, it also sends the data
   * to the Back-End so it gets stored in the user's profile
   */
  onSubmitSurfBoard = () => {
    let surfboardData = {
      weight: this.state.weight,
      height: this.state.feet,
      size: this.state.surfboardSize,
      level: this.state.expLevel,
      waveSize: this.state.waveHeight,
      productUrl: this.generateSurfboardUrl(),
    };

    if (this.props.auth0.isAuthenticated) {
      axios
        .put(
          `${urls.BACKEND_URL}/usersurfboard/${this.props.auth0.user.sub}/`,
          surfboardData
        )
        .then((response) => {
          // console.log(response);
        });
    }
    this.props.setSurfboardResults(this.state, surfboardData.productUrl);
    this.props.triggerResults();
  };

  render() {
    return (
      <Card>
        <Card.Header>
          <h4 className="text-center" style={{ margin: " 0px" }}>
            Surfboard Questions
          </h4>
        </Card.Header>
        <Card.Body>
          <Form>
            <Form.Group>
              <h5>Q1. What is your weight?</h5>
              <Form.Control
                placeholder="Enter weight in kg"
                // value={this.state.weight}
                onChange={this.handleWeightChange}
              />
            </Form.Group>
            <h5>Q2. What is your height?</h5>
            <Form.Group style={{ display: "flex" }}>
              <Form.Control
                style={{ width: "80px", margin: "0px 10px 0px 10px" }}
                placeholder="ft"
                onChange={(e) => this.handlefeetChange(e)}
              />
              <Form.Control
                style={{ width: "80px", margin: "0px 10px 0px 10px" }}
                placeholder="in"
                onChange={(e) => this.handleInchesChange(e)}
              />
            </Form.Group>
            <Form.Group>
              <h5>Q3. What is your size preference for surfboard?</h5>
              <Form.Control
                as="select"
                size="sm"
                value={this.state.surfboardSize}
                onChange={(e) => this.handleSurfboardSizeChange(e)}
              >
                <option value="longboard">longboard</option>
                <option value="shortboard">shortboard</option>
                <option value="funboard">funboard</option>
              </Form.Control>
            </Form.Group>
            <Form.Group>
              <h5>Q4. What level surfer are you?</h5>
              <Form.Control
                as="select"
                size="sm"
                value={this.state.expLevel}
                onChange={(e) => this.handleExpLevelChange(e)}
              >
                <option value="SUPER BEGINNER">SUPER BEGINNER</option>
                <option value="BEGINNER">BEGINNER</option>
                <option value="INTER / BEG">INTER / BEG</option>
                <option value="INTERMEDIATE">INTERMEDIATE</option>
                <option value="ADVAN / INTER">ADVAN / INTER</option>
                <option value="ADVANCED">ADVANCED</option>
              </Form.Control>
            </Form.Group>
            <Form.Group>
              <h5>Q5. What waves are you going to be catching?</h5>
              <Form.Control
                as="select"
                size="sm"
                value={this.state.waveHeight}
                onChange={(e) => this.handleWaveHeightChange(e)}
              >
                <option value="2 to 3 ft">2 to 3 ft</option>
                <option value="4 to 6 ft">4 to 6 ft</option>
                <option value="7 to 8 ft">7 to 8 ft</option>
                <option value="10 ft+">10 ft+</option>
              </Form.Control>
            </Form.Group>
          </Form>
        </Card.Body>

        <Button onClick={this.onSubmitSurfBoard} variant="primary" size="lg">
          Submit
        </Button>
      </Card>
    );
  }
}
export default withAuth0(SurfBoardQuestionnaire);
