import React from "react";
import { Button } from "react-bootstrap";
import "./Profile.css";
/**
 * Settings is part of a section within profile.
 * In this section the user should be able to
 * see some information such as alerts and
 * similar topics
 * 
 */
class Settings extends React.Component {
  render() {
    return (
      <div>
        <div>
          <u>Alerts Activated:</u> Yes
          <Button
            variant="primary"
            style={{ marginLeft: "5px", fontSize: "0.9rem", padding: "3px" }}
          >
            Disable
          </Button>
        </div>
        <br />
        <div>
          <u>Personal questionnaire stored on the database:</u> Yes
          {/* <Button variant="primary" style={{ marginLeft:"5px",fontSize:"0.9rem", padding:"3px"}}>Disable</Button> */}
        </div>
      </div>
    );
  }
}
export default Settings;
