import React from "react";
import WetsuitQuestionniare from "./WetsuitQuestionnaire";
import SurfBoardQuestionniare from "./SurfBoardQuestionnaire";
import SurfBoardTable from "./SurfBoardTable";
import QuestionnaireResults from "./QuestionnaireResults";
import { Card, Row, Col, Button, Container } from "react-bootstrap";
/**
 * Questionnaire acts as a container for its three subcomponents
 * WetsuitQuestionnaire, SurfboardQuestionnaire and WetsuitQuestionaireResults
 */
class Questionnaire extends React.Component {
  // the stae contains the variable that will need to be shared
  // between the subcomponents
  state = {
    showWetsuit: true,
    showSurfboard: false,
    showResults: false,
    wetSuitResults: {},
    wetSuitUrl: "",
    surfboardResults: {},
    surfboardUrl: "",
  };
  // this function is sent as props to the Wetsuit questionnaire
  // and triggers the Surfboard Questionnaire to show
  triggerShowSurfBoard = () => {
    this.setState({ showWetsuit: false, showSurfboard: true });
  };
  // this function is sent as props to the Surfboard questionnaire
  // and triggers the Questionnaire's results to show
  triggerShowResults = () => {
    this.setState({
      showWetsuit: false,
      showSurfboard: false,
      showResults: true,
    });
  };
  // this function is sent as props to the Wetsuit questionnaire
  // and stores the results of the form and the url created for 
  // the products in the state  
  setWetSuitResults = (wetSuitResults, productUrl) => {
    this.setState({ wetSuitResults: wetSuitResults, wetSuitUrl: productUrl });
  };
  // this function is sent as props to the Surfboard questionnaire
  // and stores the results of the form and the url created for 
  // the products in the state 
  setSurfboardResults = (surfboardResults, productUrl) => {
    this.setState({
      surfboardResults: surfboardResults,
      surfboardUrl: productUrl,
    });
  };
  render() {
    return (
      <div>
        <Card>
          <Card.Header as="h5">
            Surf Gear Recommendation Questionnaire
          </Card.Header>
          <Card.Body>
            <Card.Text>
              Answers the questions below to get custom surf gear
              recommendations.
            </Card.Text>
            <Container fluid>
              <Row>
                {this.state.showWetsuit && (
                  <Col>
                    <WetsuitQuestionniare
                      triggerSurf={this.triggerShowSurfBoard}
                      setWetSuitResults={this.setWetSuitResults}
                    />
                  </Col>
                )}

                {this.state.showSurfboard && (
                  <Col>
                    <SurfBoardQuestionniare
                      triggerResults={this.triggerShowResults}
                      setSurfboardResults={this.setSurfboardResults}
                    />
                  </Col>
                )}

                {this.state.showResults && (
                  <Col>
                    <QuestionnaireResults
                      questionnaireResults={{
                        wetSuitResults: this.state.wetSuitResults,
                        surfboardResults: this.state.surfboardResults,
                        wetSuitUrl: this.state.wetSuitUrl,
                        surfboardUrl: this.state.surfboardUrl,
                      }}
                    />
                  </Col>
                )}

                {this.state.showSurfboard && <SurfBoardTable />}
              </Row>
            </Container>
          </Card.Body>
        </Card>
      </div>
    );
  }
}

export default Questionnaire;
