import React from "react";
import Post from "./Post";

export default class PostList extends React.Component {
  state = {
    posts: {},
    postFlag: true,
  };

  getPosts = (posts, postFlag) => {
    this.setState({ posts: posts, postFlag: postFlag });
  };

  render() {
    let posts = this.state.postFlag ? this.props.posts : this.state.posts;
    let postList = Object.values(posts).map((post) => (
      <Post post={post} userID={this.props.userID} getPosts={this.getPosts} />
    ));

    return <div>{postList}</div>;
  }
}
