import React from "react";
import { BrowserRouter, Route } from "react-router-dom";
import { useAuth0 } from "@auth0/auth0-react";
import Sidebar from "./Sidebar";
import SecretSurfSpot from "./SecertSurfSpot/SecretSurfSpot";
import FantasyLeagueTeam from "./FantasyLeagueTeam";
import Settings from "./Settings";
import Questionnaire from "./Questionnaire/Questionnaire";
import "./Profile.css";
import { useEffect } from "react";
import axios from "axios";
/**
 * Profile is a component containing information related
 * to the current user
 */
const Profile = () => {
  const { user, isAuthenticated } = useAuth0();

  return (
    isAuthenticated && (
      <div class="profile-container">
        <BrowserRouter>
          <Sidebar />,
          <div id="profile-content" style={{ padding: "50px" }}>
            <div style={{ fontSize: "1.5rem" }}>Welcome {user.nickname}</div>
            <br />
            <Route path="/Settings" component={Settings}></Route>
            <Route path="/SecretSurfSpot" component={SecretSurfSpot}></Route>
            <Route
              path="/FantasyLeagueTeam"
              component={FantasyLeagueTeam}
            ></Route>
            <Route path="/Questionnaire" component={Questionnaire}></Route>
          </div>
        </BrowserRouter>
      </div>
    )
  );
};

export default Profile;
