import React from "react";
import { Card, Form, Button } from "react-bootstrap";
import { useAuth0, withAuth0 } from "@auth0/auth0-react";
import axios from "axios";
import * as urls from "../../urls";
/**
 * WetsuitQuestionnaire contains all the information
 * regarding the questionnaire for wetsuits
 */
class WetsuitQuestionnaire extends React.Component {
  // state contains all the variables that need to be
  // shared. Some variables have a default based on
  // the dropdown
  state = {
    gender: "",
    size: "size_xs",
    waterTemp: "65°-75°",
    coldness: "",
    zipper: "",
  };
  /**
   * handleGenderChange stores the form option chosen
   * by the user in the state for the gender option
   * @param {*} e
   */
  handleGenderChange = (e) => {
    this.setState({ gender: e.target.value });
  };
  /**
   * handleSizeChange stores the form option chosen
   * by the user in the state for the wetsuit size option
   * @param {*} e
   */
  handleSizeChange = (e) => {
    this.setState({ size: e.target.value });
  };
  /**
   * handleWaterTempChange stores the form option chosen
   * by the user in the state for the water temperature option
   * @param {*} e
   */
  handleWaterTempChange = (e) => {
    this.setState({ waterTemp: e.target.value });
  };
  /**
   * handleColdSensitivityChange stores the form option chosen
   * by the user in the state for the sensitivity to cold option
   * @param {*} e
   */
  handleColdSensitivityChange = (e) => {
    this.setState({ coldness: e.target.value });
  };
  /**
   * handleZipperTypeChange stores the form option chosen
   * by the user in the state for the wetsuit zipper option
   * @param {*} e
   */
  handleZipperTypeChange = (e) => {
    this.setState({ zipper: e.target.value });
  };
  /**
   * generateWetsuitUrl creates a url based on the user's inputs.
   * thicknessObj is and object whose values match the possible
   * urls parts (of the providers API) to a temperature range
   * @returns String (URL)
   */
  generateWetsuitUrl = () => {
    let url = `https://www.evo.com/shop/surf/wetsuits/${this.state.gender}/${this.state.size}/`;
    let thicknessObj = {
      "65°-75°": [
        "thickness_0_5-mm",
        "thickness_1-0_5-mm",
        "thickness_1-5-mm",
        "thickness_1-mm",
        "thickness_2-1-mm",
        "thickness_2-mm",
        "wetsuit-thickness_1_5-1-mm",
      ],
      "62°-68°": [
        "thickness_2-1-mm",
        "thickness_2-2-mm",
        "thickness_2-mm",
        "thickness_3-2-mm",
        "thickness_3-mm",
      ],
      "58°-63°": [
        "thickness_3-2-mm",
        "thickness_3-mm",
        "thickness_4_5-3_5-mm",
        "thickness_4-2-mm",
        "thickness_4-3-mm",
        "thickness_4-mm",
      ],
      "52°-58°": [
        "thickness_4_5-3_5-mm",
        "thickness_4-3-mm",
        "thickness_5-4-mm",
        "thickness_5-mm",
        "thickness_mm",
        "wetsuit-thickness_5-3-mm",
      ],
      "43°-52°": [
        "thickness_5-4-mm",
        "thickness_5-mm",
        "thickness_mm",
        "wetsuit-thickness_5_5-4_5-3-mm",
        "wetsuit-thickness_5_5-4_5-mm",
        "wetsuit-thickness_5_5-4-mm",
        "wetsuit-thickness_5-3-mm",
      ],
      "42° and below": [
        "thickness_7-mm",
        "thickness_mm-2",
        "wetsuit-thickness_6-5-mm",
        "wetsuit-thickness_6-mm",
        "wetsuit-thickness_7-5-mm",
      ],
    };
    let thicknessList = thicknessObj[this.state.waterTemp];
    thicknessList.forEach((thickness) => (url += thickness + "/"));
    url += this.state.zipper;
    return url;
  };
  /**
   * onSubmitWetsuit sends the callback containing the results
   * to props. If the user is logged in, it also sends the data
   * to the Back-End so it gets stored in the user's profile
   */
  onSubmitWetsuit = () => {
    let selectedUrl = this.generateWetsuitUrl();

    let wetsuitData = {
      gender: this.state.gender,
      size: this.state.size,
      waterTemp: this.state.waterTemp,
      coldSensitivy: this.state.coldness,
      zipperType: this.state.zipper,
      productUrl: selectedUrl,
    };

    if (this.props.auth0.isAuthenticated) {
      axios
        .put(
          `${urls.BACKEND_URL}/userwetsuit/${this.props.auth0.user.sub}/`,
          wetsuitData
        )
        .then(function (response) {
          // console.log(response.data);
        })
        .catch((err) => {
          // console.log(err);
        });
    }
    this.props.setWetSuitResults(this.state, selectedUrl);
    this.props.triggerSurf();
  };

  render() {
    return (
      <Card>
        <Card.Header>
          <h4 className="text-center" style={{ margin: " 0px" }}>
            Wetsuit Questions
          </h4>
        </Card.Header>
        <Card.Body>
          <Form>
            <Form.Group onChange={(e) => this.handleGenderChange(e)}>
              <h5>Q1. What is your Gender?</h5>
              <Form.Check
                type="radio"
                name="gender"
                value="mens"
                label={"Men"}
              />
              <Form.Check
                type="radio"
                name="gender"
                value="womens"
                label={"Women"}
              />
            </Form.Group>
            <Form.Group>
              <h5>Q2. What is your size?</h5>
              <Form.Control
                as="select"
                size="sm"
                value={this.state.size}
                onChange={(e) => this.handleSizeChange(e)}
              >
                <option value="size_xs">XS</option>
                <option value="size_s">S</option>
                <option value="size_s-m">S/M</option>
                <option value="size_st">ST</option>
                <option value="size_ms">MS</option>
                <option value="size_m">M</option>
                <option value="size_mt">MT</option>
                <option value="size_ml">ML</option>
                <option value="size_ls">LS</option>
                <option value="size_l">L</option>
                <option value="size_l-x">L/X</option>
                <option value="size_l-xl">L/XL</option>
              </Form.Control>
            </Form.Group>
            <Form.Group>
              <h5>Q3. What is the Water Temperature?</h5>
              <Form.Control
                as="select"
                size="sm"
                value={this.state.waterTemp}
                onChange={(e) => this.handleWaterTempChange(e)}
              >
                <option value="65°-75°">65°-75°</option>
                <option value="62°-68°">62°-68°</option>
                <option value="58°-63°">58°-63°</option>
                <option value="52°-58°">52°-58°</option>
                <option value="43°-52°"> 43°-52°</option>
                <option value="42° and below">42° and below</option>
              </Form.Control>
            </Form.Group>
            <Form.Group>
              <h5>Q4. Do you get cold easily?</h5>
              <Form.Check
                name="coldness"
                type="radio"
                label={"really cold"}
                value="really cold"
                onChange={(e) => this.handleColdSensitivityChange(e)}
              />
              <Form.Check
                name="coldness"
                type="radio"
                label={"somewhat cold"}
                onChange={(e) => this.handleColdSensitivityChange(e)}
              />
              <Form.Check
                name="coldness"
                type="radio"
                label={"I like coldness"}
                value="I like coldness"
                onChange={(e) => this.handleColdSensitivityChange(e)}
              />
            </Form.Group>
            <Form.Group>
              <h5>Q5. What type of Zip do you want?</h5>
              <Form.Check
                name="zipper"
                type="radio"
                label={"Back Zip"}
                value="wetsuit-fit_back-zip"
                onChange={(e) => this.handleZipperTypeChange(e)}
              />
              <Form.Check
                name="zipper"
                type="radio"
                label={"Chest Zip"}
                value="wetsuit-fit_chest-zip"
                onChange={(e) => this.handleZipperTypeChange(e)}
              />
              <Form.Check
                name="zipper"
                type="radio"
                label={"Zipperless"}
                value={"wetsuit-fit_zipperless"}
                onChange={(e) => this.handleZipperTypeChange(e)}
              />
            </Form.Group>
          </Form>
        </Card.Body>
        <Button onClick={this.onSubmitWetsuit} variant="primary" size="lg">
          Next
        </Button>
      </Card>
    );
  }
}
export default withAuth0(WetsuitQuestionnaire);
