import React, { Component } from "react";
import { Editor } from "react-draft-wysiwyg";
import { Button } from "react-bootstrap";
import { EditorState, convertToRaw } from "draft-js";
import axios from "axios";
import "./PostCategory.css";
import { withAuth0 } from "@auth0/auth0-react";
import * as urls from "../../../urls";

class NewPost extends Component {
  state = {
    contentState: EditorState.createEmpty(),
  };

  onEditorStateChange = (contentState) => {
    this.setState({
      contentState: contentState,
    });
  };

  setUser = () => {
    const { user } = this.props.auth0;
    return user;
  };

  onSubmitPost = (event) => {
    event.preventDefault();

    let postContentObj = {
      thread_id: this.props.threadID,
      content: JSON.stringify(
        convertToRaw(this.state.contentState.getCurrentContent())
      ),
      creator: this.setUser().sub,
    };
    axios
      .post(`${urls.BACKEND_URL}/api/post/create/`, postContentObj)
      .then((response) => {
        axios
          .get(`${urls.BACKEND_URL}/api/post/getPosts/${this.props.threadID}/`)
          .then((postResponse) => {
            this.props.getPosts(postResponse.data, false);
          });
        this.props.hideEditor(false);
      });
  };

  render() {
    return (
      <div>
        <Editor
          wrapperClassName="wrapper-class"
          editorClassName="editor-class"
          toolbarClassName="toolbar-class"
          onEditorStateChange={this.onEditorStateChange}
        />
        <div className="topic-button">
          <Button
            onClick={this.onSubmitPost}
            className="forum-buttom"
            variant="primary"
          >
            Submit
          </Button>
        </div>
      </div>
    );
  }
}

export default withAuth0(NewPost);
