import React from "react";
import Nav from "react-bootstrap/Nav";
import Form from "react-bootstrap/Form";
import FormControl from "react-bootstrap/FormControl";
import Button from "react-bootstrap/Button";
import Navbar from "react-bootstrap/Navbar";
import AuthenticationButton from "./AuthenticationButton";
import history from "../../history";
import logo from "./surfscope.png";

class NavigationBar extends React.Component {
  state = { term: "" };
  /**
   * This function is used when a user enters the name of a surfing spot and clicks on submit
   * The user will then be redirected to the surf spot page
   * @param {*} event
   */
  onFormSubmit = (event) => {
    event.preventDefault();
    this.props.onSubmit(this.state.term);
    history.push("/spot-forecast");
  };
  /**
   * The Navbar uses bootstrapped content, to add some links, simply add a new <Nav.link> tag
   * Each Navbar Link will redirect the user to the page matching the Nav Link text
   * @returns Navbar
   */
  render() {
    return (
      <Navbar
        style={{ height: "3%", fontSize: "1.4rem" }}
        sticky="top"
        class="navbar"
        bg="primary"
        variant="dark"
        expand="lg"
      >
        <Navbar.Brand href="/">
          <img
            width="198px"
            height="42p"
            height="auto"
            className="img-responsive"
            src={logo}
            alt="SurfScopeForecast"
          />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link href="/">
              <div class="nav-a">Home</div>
            </Nav.Link>
            <Nav.Link href="/forum">
              <div class="nav-a">Forum</div>
            </Nav.Link>
            <Nav.Link href="/maps">
              <div class="nav-a">Maps</div>
            </Nav.Link>
            <Nav.Link href="/fantasyleague">
              <div class="nav-a">Fantasy League</div>
            </Nav.Link>
            <Nav.Link href="/profile">
              <div class="nav-a">Profile</div>
            </Nav.Link>
            <AuthenticationButton style={{ float: "right" }} />
          </Nav>
          <Form inline onSubmit={this.onFormSubmit}>
            <FormControl
              style={{ margin: "5px 0px 5px 0px" }}
              onChange={(e) => this.setState({ term: e.target.value })}
              value={this.state.term}
              type="text"
              placeholder="Search"
              className="mr-sm-2"
            />
            <Button onClick={this.onFormSubmit} variant="outline-light">
              Search
            </Button>
          </Form>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}

export default NavigationBar;
