import React from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Badge from "react-bootstrap/Badge";
import "./TopicCategory.css";
import history from "../../../history";
import axios from "axios";
import moment from "moment";
import * as urls from "../../../urls";

export class TopicCategory extends React.Component {
  state = {
    lastPost: "",
    userLastPost: "",
    timeLastPost: "",
    metaLoading: false,
  };
  onTopicClick = (e, topicId, topicObj) => {
    e.preventDefault();
    axios
      .get(`${urls.BACKEND_URL}/api/post/getPosts/${topicId}/`)
      .then((postResponse) => {
        this.props.getPosts(postResponse.data);
      });
    history.push(`/topics/${this.props.slug}/${this.props.topicId}`);
    this.props.setTopic(topicId, topicObj);
  };
  getTopicMetaData = () => {
    axios
      .get(
        `${urls.BACKEND_URL}/api/thread/getThreadMetaData/${this.props.topicId}/`
      )
      .then((res) => {
        let lastPost = "";
        let lastTimePost = "";
        let lastUsername = "";
        if (typeof res.data.data == "string") {
          lastPost = res.data.data;
        } else {
          let contentHTML = JSON.parse(res.data.data.content);
          let contentText = contentHTML["blocks"][0]["text"];
          lastPost = contentHTML["blocks"][0]["text"];
          if (contentText.length > 30) {
            lastPost = lastPost.substring(0, 30) + "...";
          }
          let lastDateObj = new Date(res.data.data.created_at);
          lastTimePost = moment(lastDateObj).format("lll");
          lastUsername = res.data.data.username;
        }
        this.setState({
          lastPost: lastPost,
          userLastPost: lastUsername,
          timeLastPost: lastTimePost,
          metaLoading: true,
        });
      });
  };

  componentDidMount() {
    this.getTopicMetaData();
  }

  render() {
    let postedTime = new Date(this.props.postedTime);
    return (
      <div className="topic-container">
        <Row>
          <Col sm={8}>
            <a
              onClick={(e) =>
                this.onTopicClick(e, this.props.topicId, this.props.topicObj)
              }
              href={`/topics/${this.props.slug}/${this.props.topicId}`}
            >
              {this.props.title}
            </a>
            <p>
              <span>created by: </span>
              <Badge variant="primary">{this.props.userName}</Badge>
            </p>
          </Col>
          {this.state.metaLoading ? (
            <Col sm={4}>
              <div className="divider"></div>
              <p className="last-post">{this.state.lastPost}</p>
              <p className="post-preview">
                <Badge variant="primary">{this.state.userLastPost}</Badge>
              </p>
              <p className="post-preview-date">{this.state.timeLastPost}</p>
            </Col>
          ) : (
            <Col sm={4}>
              <div class="divider"></div>
              <div class="loader"></div>
            </Col>
          )}
        </Row>
      </div>
    );
  }
}

export default TopicCategory;
