import React from "react";
import "./PostCategory.css";
import PostContainer from "./PostContainer";
import axios from "axios";
import { Editor } from "react-draft-wysiwyg";
import { InputGroup, FormControl } from "react-bootstrap";
import draftToHtml from "draftjs-to-html";
import parse from "html-react-parser";
import { convertFromRaw, convertToRaw, EditorState } from "draft-js";
import { Modal, Card, Button } from "react-bootstrap";
import history from "../../../history";
import { withAuth0 } from "@auth0/auth0-react";
import * as urls from "../../../urls";

class ForumTopic extends React.Component {
  contentState = JSON.parse(this.props.topicObj.content);
  editorState = EditorState.createWithContent(
    convertFromRaw(this.contentState)
  );
  contentHTML = draftToHtml(this.contentState);
  reactElement = parse(this.contentHTML);

  state = {
    showEditor: false,
    title: "",
    contentState: this.editorState,
    show: false,
  };

  onApplySubmit = () => {
    let updatedTopic = {
      name: this.state.title,
      content: JSON.stringify(
        convertToRaw(this.state.contentState.getCurrentContent())
      ),
    };
    axios
      .put(
        `${urls.BACKEND_URL}/api/thread/${this.props.topicObj.id}/edit/`,
        updatedTopic
      )
      .then((response) => {
        history.push(`/topics/${this.props.slug}`);
      });
  };

  onCancelEdit = () => {
    this.setState({ showEditor: false });
  };

  onEditClick = () => {
    this.setState({ showEditor: true });
  };

  onEditorStateChange = (contentState) => {
    this.setState({
      contentState: contentState,
    });
  };

  onDeleteClick = () => {
    this.setState({ show: true });
  };

  onConfirmClick = () => {
    axios
      .delete(
        `${urls.BACKEND_URL}/api/thread/${this.props.topicObj.id}/delete/`
      )
      .then((postResponse) => {
        console.log(postResponse.data);
        history.push(`/topics/${this.props.slug}`);
      });
  };

  render() {
    return (
      <div>
        {this.state.showEditor ? (
          <PostContainer
            title="Topic"
            posts={this.props.posts}
            userID={this.props.userID}
            threadID={this.props.topicObj.id}
            slug={this.props.slug}
          >
            <Card border={this.props.cardStyle}>
              <Card.Body>
                <InputGroup className="mb-3">
                  <FormControl
                    onChange={(e) => this.setState({ title: e.target.value })}
                    defaultValue={this.props.topicObj.name}
                  />
                </InputGroup>
                <div className="place-holder">
                  <Editor
                    wrapperClassName="wrapper-class"
                    editorClassName="editor-class"
                    toolbarClassName="toolbar-class"
                    onEditorStateChange={this.onEditorStateChange}
                    defaultEditorState={this.editorState}
                  />
                </div>
                <div className="topic-button">
                  <Button
                    onClick={this.onApplySubmit}
                    className="forum-buttom"
                    variant="primary"
                  >
                    Apply
                  </Button>
                  <Button
                    className="forum-buttom"
                    onClick={this.onCancelEdit}
                    variant={this.props.buttonColor}
                  >
                    Cancel
                  </Button>
                </div>
              </Card.Body>
            </Card>
          </PostContainer>
        ) : (
          <PostContainer
            title="Topic"
            posts={this.props.posts}
            userID={this.props.userID}
            threadID={this.props.topicObj.id}
            slug={this.props.slug}
          >
            <Card border={this.props.cardStyle}>
              <Card.Body>
                <Card.Title>{this.props.topicObj.name} </Card.Title>
                <Card.Subtitle className="mb-2 text-muted">
                  by: {this.props.topicObj.username}{" "}
                </Card.Subtitle>
                <Card.Text></Card.Text>
                {this.reactElement}
                {this.props.userID == this.props.topicObj.creator_id && (
                  <div className="topic-button">
                    <Button
                      onClick={this.onEditClick}
                      className="forum-buttom"
                      variant="primary"
                    >
                      Edit
                    </Button>
                    <Button
                      className="forum-buttom"
                      variant={this.props.buttonColor}
                      onClick={this.onDeleteClick}
                    >
                      Delete
                    </Button>
                  </div>
                )}
                <Modal
                  size="sm"
                  show={this.state.show}
                  onHide={() => this.setState({ show: false })}
                  aria-labelledby="example-modal-sizes-title-sm"
                >
                  <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-sm">
                      Delete Topic?
                    </Modal.Title>
                  </Modal.Header>
                  <Modal.Body>
                    <div className="topic-button">
                      <Button
                        className="forum-buttom"
                        variant="primary"
                        onClick={this.onConfirmClick}
                      >
                        Confirm
                      </Button>
                      <Button
                        className="forum-buttom"
                        variant="danger"
                        onClick={() => this.setState({ show: false })}
                      >
                        Cancel
                      </Button>
                    </div>
                  </Modal.Body>
                </Modal>
              </Card.Body>
            </Card>
          </PostContainer>
        )}
      </div>
    );
  }
}

export default withAuth0(ForumTopic);
