import React from "react";
import { useState } from "react";
import { Card, Button, Table, Col } from "react-bootstrap";
import AddSpot from "./AddSpot";
import SecretSpots from "./SecretSpots";
import { useAuth0 } from "@auth0/auth0-react";
import { useEffect } from "react";
import axios from "axios";
import useDidMountEffect from "./useDidMountEffect";
import * as urls from "../../../urls";
/**
 * SecretSurfSpot acts as the global container for
 * each of the secret surf spot components
 * @returns
 */
const SecretSurfSpot = () => {
  // variables that will retreve data from requests
  // and or variable with data to be displayed
  const { user, isAuthenticated } = useAuth0();
  const [showAddSpot, setShowAddSpot] = useState(false);
  const [spots, setSpots] = useState([]);
  const [flag, setFlag] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  /**
   * useEffect is used to call fetchId() and
   * to cause a rerender
   */
  useEffect(() => {
    fetchId(user.sub);
  }, []);
  /**
   * Fetches a single user's surfing info
   * @param {*} id
   */
  const fetchId = (id) => {
    axios
      .get(`${urls.BACKEND_URL}/surfingInfo/${id}/`)
      .then((res) => {
        let result = res.data.secretList;
        result = JSON.parse(result.replace(/'/g, '"'));
        // console.log(result);
        // console.log(typeof result);
        const fetchedSpots = [];
        Object.keys(result).forEach(function (key) {
          fetchedSpots.push(result[key]);
        });
        // console.log(fetchedSpots[0] !== "{");
        if (fetchedSpots[0] !== "{") {
          setSpots(fetchedSpots);
        }
        setIsLoading(false);
      })
      .catch((error) => {
        console.log(error);
      });
  };
  /**
   * addSpot concatenates the current list
   * of secret surf spots, to the newly
   * added element
   * @param {*} spot
   */
  const addSpot = (spot) => {
    // const newSpot = { num, ...spot };
    const newSpot = { ...spot };
    setSpots([...spots, newSpot]);
    flag ? setFlag(false) : setFlag(true);
  };

  /**
   * updateSss is used to update existing
   * secretsurfspots, both in the front-end
   * and the back-end
   * @param {*} id
   * @param {*} jsonSpots
   */
  const updateSss = (id, jsonSpots) => {
    axios
      .put(`${urls.BACKEND_URL}/surfingInfo/${id}/`, jsonSpots, {
        headers: { "Content-Type": "application/json" },
      })
      .then((res) => {})
      .catch((error) => {
        console.log(error);
      });
  };
  /**
   * useDidMountEffect ensures that the data gets set in the
   * state once the component is ready
   */
  useDidMountEffect(() => {
    const updatedSpots = { ...spots };
    updateSss(user.sub, updatedSpots);
  }, [flag]);
  /**
   * deleteSpot is used to delete existing
   * secretsurfspots, both in the front-end
   * and the back-end
   * @param {*} id
   * @param {*} jsonSpots
   */
  const deleteSpot = (spotName) => {
    setSpots(spots.filter((spot) => spot.spotName !== spotName));
    flag ? setFlag(false) : setFlag(true);
  };

  return (
    <div>
      <Card border="secondary" style={{ width: "80%" }}>
        <Card.Header>
          Current Secret Surf Spots List:
          <Button
            style={{ float: "right" }}
            onClick={() => setShowAddSpot(!showAddSpot)}
          >
            {showAddSpot ? "Cancel" : "Edit"}
          </Button>
        </Card.Header>
        {isLoading ? (
          <Col sm={4} style={{ marginLeft: "350px", marginBottom: "20px" }}>
            <div class="loader"></div>
          </Col>
        ) : (
          <Card.Body>
            <Card.Text>
              <div>
                {showAddSpot && <AddSpot onAdd={addSpot} />}
                {spots.length > 0 ? (
                  <SecretSpots
                    onDelete={deleteSpot}
                    onShow={showAddSpot}
                    spots={spots}
                  />
                ) : (
                  "No spots to show! Click on edit to add some."
                )}
              </div>
            </Card.Text>
          </Card.Body>
        )}
      </Card>
    </div>
  );
};

export default SecretSurfSpot;
