import React from "react";
import PropTypes from "prop-types";
import { DragDropContext } from "react-beautiful-dnd";
import axios from "axios";
import SurferCard from "../SurferCard";
import Column from "./Column";
import styled from "styled-components";
import SurferSelectionModal from "./SurferSelectionModal";
import { Col } from "react-bootstrap";
import "./FantasyLeagueStyle.css";
import * as urls from "../../../urls"

const Container = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 40px;
`;

export class TeamSelector extends React.Component {
  setShowModal = () => {
    this.setState({ showModal: false });
  };

  generateSurferData = () => {
    axios
      .get(`${urls.BACKEND_URL}/api/fantasyleague/surfers/`)
      .then((res) => {
        res = res.data;
        var initialData = {};
        var all_ids = [];
        for (var i = 0; i < res.length; i++) {
          var surfer_id = "surfer-" + i;

          all_ids.push(surfer_id);
          initialData = {
            surfers: {
              ...initialData.surfers,
              [surfer_id]: {
                id: surfer_id,
                name: res[i]["name"],
                headshot: res[i]["athlete_photo"],
                flag: res[i]["athlete_country_flag"],
                score: res[i]["tour_points"],
              },
            },
            columns: {
              "column-1": {
                id: "column-1",
                title: "Surfer Selection : ",
                surfersIds: all_ids,
              },
              "column-2": {
                id: "column-2",
                title: "Your Team: ",
                surfersIds: [],
              },
            },
            isUnlocked: false,
            showModal: false,
            selectedSurfers: [],
            // Helps with the order of columns
            columnOrder: ["column-1", "column-2"],
          };
        }
        this.setState(initialData);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  componentDidMount() {
    this.generateSurferData();
  }

  onDragEnd = (result) => {
    const { destination, source, draggableId } = result;

    if (!destination) {
      return;
    }

    if (
      destination.droppableId === source.droppableId &&
      destination.index === source.index
    ) {
      return;
    }

    const start = this.state.columns[source.droppableId];
    const finish = this.state.columns[destination.droppableId];

    if (start === finish) {
      const newSurferIds = Array.from(start.surfersIds);
      newSurferIds.splice(source.index, 1);
      newSurferIds.splice(destination.index, 0, draggableId);

      const newColumn = {
        ...start,
        surfersIds: newSurferIds,
      };

      const newState = {
        ...this.state,
        columns: {
          ...this.state.columns,
          [newColumn.id]: newColumn,
        },
      };

      this.setState(newState);
      return;
    }

    //Moving from one list to another
    var startSurferIds = Array.from(start.surfersIds);

    startSurferIds.splice(source.index, 1);
    var newStart = {
      ...start,
      surfersIds: startSurferIds,
    };

    var finishSurferIds = Array.from(finish.surfersIds);
    finishSurferIds.splice(destination.index, 0, draggableId);

    var newFinish = {
      ...finish,
      surfersIds: finishSurferIds,
    };
    const newState = {
      ...this.state,
      columns: {
        ...this.state.columns,
        [newStart.id]: newStart,
        [newFinish.id]: newFinish,
      },
    };

    if (newState.columns["column-2"]["surfersIds"].length >= 4) {
      newState.showModal = true;
      newState.isUnlocked = true;
    } else {
      newState.isUnlocked = false;
    }

    var surfers = [];
    var selectedSurfers = newState.columns["column-2"]["surfersIds"];
    selectedSurfers.forEach((surferId) =>
      surfers.push(newState.surfers[surferId])
    );
    newState.selectedSurfers = surfers;

    this.setState(newState);
  };
  render() {
    if (!this.state) {
      return (
        <div class="loader-container">
          <div class="center">
            <p>Team selection is loading...</p>
            <div class="loader"></div>
          </div>
        </div>
      );
    }
    return (
      <Container>
        <DragDropContext onDragEnd={this.onDragEnd}>
          {this.state.columnOrder.map((columnId) => {
            const column = this.state.columns[columnId];
            const surfers = column.surfersIds.map(
              (surferId) => this.state.surfers[surferId]
            );
            return (
              <Column
                showRanking={this.props.showRanking}
                isUnlocked={this.state.isUnlocked}
                key={column.id}
                column={column}
                surfers={surfers}
                selectedSurfersList={this.state.selectedSurfers}
                topFive={this.props.topFive}
              />
            );
          })}
        </DragDropContext>
      </Container>
    );
  }
}

export default TeamSelector;
