import React from "react";
import { useState } from "react";
import { Form, Col, Button } from "react-bootstrap";
/**
 * AddSpot is a functional component that gets loaded
 * when the user requests to add a new secret surf spot
 * in the Profile page.
 * @param {*} param0 
 * @returns 
 */
const AddSpot = ({ onAdd }) => {
  // variables that will store the necessary data
  const [x, setX] = useState("");
  const [y, setY] = useState("");
  const [minSwell, setMinSwell] = useState("");
  const [maxWind, setMaxWind] = useState("");
  const [spotName, setSpotName] = useState("");
  const coordinates = { x, y };
  /**
   * onSubmit gets triggered when the user submits the form.
   * If any of the values are not valid, an alert message is displayed.
   * Otherwise, the value are stored
   * @param {*} e 
   * @returns 
   */

  const onSubmit = (e) => {
    e.preventDefault();
    if (!x || !y || !minSwell || !maxWind || !spotName) {
      alert("Please add a spot");
      return;
    }
    onAdd({ coordinates, minSwell, maxWind, spotName });
    setX("");
    setY("");
    setMinSwell("");
    setMaxWind("");
    setSpotName("");
  };

  return (
    <div className="sss-profile">
      <Form onSubmit={onSubmit}>
        <Form.Row>
          <Col xs={2}>
            <Form.Label>Spot name</Form.Label>
            <Form.Control
              placeholder="Spot name"
              onChange={(e) => setSpotName(e.target.value)}
            />
          </Col>
          <Col xs={2}>
            <Form.Label>X Coordinate</Form.Label>
            <Form.Control
              placeholder="X"
              onChange={(e) => setX(e.target.value)}
            />
          </Col>
          <Col xs={2}>
            <Form.Label>Y Coordinate</Form.Label>
            <Form.Control
              placeholder="Y"
              onChange={(e) => setY(e.target.value)}
            />
          </Col>
          <Col xs={2}>
            <Form.Label>Min Swell</Form.Label>
            <Form.Control
              placeholder="ft"
              onChange={(e) => setMinSwell(e.target.value)}
            />
          </Col>
          <Col xs={2}>
            <Form.Label>Max Wind</Form.Label>
            <Form.Control
              placeholder="kts"
              onChange={(e) => setMaxWind(e.target.value)}
            />
          </Col>
          <Col className="add-btn" xs={2}>
            <Button type="Submit"> Add </Button>
          </Col>
        </Form.Row>
      </Form>
    </div>
  );
};

export default AddSpot;
