import React from "react";
import { Card, Container, Row, Col } from "react-bootstrap";
import { withAuth0 } from "@auth0/auth0-react";
import SurfboardQuestionnaireResult from "./SurfboardQuestionnaireResult";
import WetsuitQuestionnaireResult from "./WetSuitQuestionnaireResult";

/**
 * QuestionnaireResults consists in two tables containing
 * the values retrieved from WetsuitQuestionnaire
 * and SurfboardQuestionnaire
 */
class QuestionnaireResults extends React.Component {
  render() {
    return (
      <Container fluid>
        <Row>
          <Col>
            <WetsuitQuestionnaireResult
              wetSuitResults={{
                wetSuitResults: this.props.questionnaireResults.wetSuitResults,
                wetSuitUrl: this.props.questionnaireResults.wetSuitUrl,
              }}
            />
          </Col>
          <Col>
            <SurfboardQuestionnaireResult
              surfboardResults={{
                surfboardResults: this.props.questionnaireResults
                  .surfboardResults,
                surfboardUrl: this.props.questionnaireResults.surfboardUrl,
              }}
            />
          </Col>
        </Row>
      </Container>
    );
  }
}
export default withAuth0(QuestionnaireResults);
