import React from "react";
import { Card, Table, Col } from "react-bootstrap";
import axios from "axios";
import { withAuth0 } from "@auth0/auth0-react";
import "./Questionnaire.css";
import * as urls from "../../../urls";

/**
 * SurfBoardResults contains the user's last results
 * for the SurfBoard part of the questionnaire
 */
class SurfBoardResults extends React.Component {
  // state stores data from requests to be displayed
  state = {
    weight: "",
    height: "",
    size: "",
    level: "",
    waveSize: "",
    productUrl: "",
    loading: false,
  };
  /**
   * getSurfboardData does an initial request to retrieve
   * the board results of the user and stores the results
   * in the state
   */
  getSurfboardData() {
    axios
      .get(`${urls.BACKEND_URL}/usersurfboard/${this.props.auth0.user.sub}/`)
      .then((response) => {
        console.log(response);
        this.setState({
          weight: response.data.weight,
          height: response.data.height,
          size: response.data.size,
          level: response.data.level,
          waveSize: response.data.waveSize,
          productUrl: response.data.productUrl,
          loading: true,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  /**
   * componentDidMount ensures that the data gets set in the
   * state once the component is ready
   */
  componentDidMount() {
    this.getSurfboardData();
  }
  render() {
    console.log(this.props.auth0.user.sub);
    return (
      <Card>
        <Card.Header>
          <h4 className="text-center" style={{ margin: " 0px" }}>
            Surfboard Results
          </h4>
        </Card.Header>

        <Card.Body>
          {this.state.loading ? (
            <>
              <Table striped bordered>
                <tbody>
                  <tr>
                    <th>Weight</th>
                    <td>{this.state.weight} lb</td>
                  </tr>
                  <tr>
                    <th>Height</th>
                    <td>{this.state.height}</td>
                  </tr>
                  <tr>
                    <th>Your Surfboard Size</th>
                    <td>{this.state.size}</td>
                  </tr>
                  <tr>
                    <th>Your surfing level</th>
                    <td>{this.state.level}</td>
                  </tr>
                  <tr>
                    <th>For wave size</th>
                    <td>{this.state.waveSize}</td>
                  </tr>
                </tbody>
              </Table>
              <a
                rel="noopener noreferrer"
                target="_blank"
                href={this.state.productUrl}
                className="btn btn-primary"
              >
                Click to view Surfboard
              </a>
            </>
          ) : (
            <div class="loader-container">
              <div class="center">
                <div class="loader"></div>
              </div>
            </div>
          )}
        </Card.Body>
      </Card>
    );
  }
}

export default withAuth0(SurfBoardResults);
