import React, { useState } from "react";
import { Container, Row, Col, Card, Button, Modal } from "react-bootstrap";
import history from "../../../history";
import "./TopicCategory.css";
import { useAuth0 } from "@auth0/auth0-react";

const TopicContainer = (props) => {
  const [show, setShow] = useState(false);
  const { user } = useAuth0();

  const onClickNewTopic = () => {
    if (user === undefined) {
      setShow(true);
    } else {
      history.push("/new-topic");
    }
  };

  return (
    <Container className="forum">
      <Row>
        <Col>
          <Card>
            <Card.Header>
              {props.title}
              <Button className="topic-button" onClick={onClickNewTopic}>
                New Topic
              </Button>
            </Card.Header>
            <Card.Body>{props.children}</Card.Body>
            {props.showMoreButton && (
              <Button
                onClick={props.onShowMoreButtonClick}
                style={{ textAlign: "center" }}
              >
                Show more topics
              </Button>
            )}
          </Card>
        </Col>
      </Row>
      <Modal
        size="sm"
        show={show}
        onHide={() => setShow(false)}
        aria-labelledby="example-modal-sizes-title-sm"
      >
        <Modal.Header closeButton>
          <Modal.Title id="example-modal-sizes-title-sm">
            Please SignUp/LogIn to create a new topic
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="topic-button">
            <Button
              className="forum-buttom"
              variant="danger"
              onClick={() => setShow(false)}
            >
              Close
            </Button>
          </div>
        </Modal.Body>
      </Modal>
    </Container>
  );
};
export default TopicContainer;
