import React from "react";
import PropTypes from "prop-types";
import { Card } from "react-bootstrap";
import "./FantasyLeagueStyle.css";
import { Draggable } from "react-beautiful-dnd";
import styled from "styled-components";

const Container = styled.div`
  margin: 8px;
  border: 1px solid lightgrey;
  border-radius: 2px;
`;

export class SurferCard extends React.Component {
  render() {
    return (
      <Card
        style={{
          width: "21rem",
          padding: "0px",
          marginBottom: "10px",
          marginTop: "10px",
        }}
      >
        <Card.Body style={{ padding: "3px 10px 0px 10px" }}>
          <div class="surfer-info-container">
            <img class="headshot-img" src={this.props.surfer.headshot}></img>
            <Card.Title>
              {this.props.surfer.name}
              <img class="flag-img" src={this.props.surfer.flag}></img>
            </Card.Title>
            {this.props.showScore && (
              <Card.Subtitle className="mb-2 text-muted">
                Score: {this.props.surfer.score}
              </Card.Subtitle>
            )}
          </div>
        </Card.Body>
      </Card>
    );
  }
}

export default SurferCard;
