import React, { Component } from "react";
import { Card, Container, Col, Row } from "react-bootstrap";
import WeatherDay from "./WeatherDay";
/**
 * WeatherDays acts as a container for each WeatherDay.
 * WeatherDays takes every day of data in props, and
 * maps each day to a WeatherDays Component
 */
export default class WeatherDays extends Component {
  render() {
    return (
      <Container fluid>
        <Row className="justify-content-center">
          {this.props.sData.map((data) => (
            <WeatherDay sData={data} />
          ))}
        </Row>
      </Container>
    );
  }
}
