import React, { Component } from "react";
import TopicContainer from "./TopicContainer";
import TopicCategory from "./TopicCategory";
import axios from "axios";
import * as urls from "../../../urls";

export class TopicCategories extends Component {
  state = { topicList: {}, numOfItemsShown: 5, showMoreButton: false };
  fetchTopics = (id) => {
    axios
      .get(`${urls.BACKEND_URL}/api/thread/getThreads/${id}/`)
      .then((res) => {
        if (Object.keys(res.data).length > 5)
          this.setState({ showMoreButton: true });
        this.setState({ topicList: res.data });
      })
      .catch((error) => {
        console.log(error);
      });
  };
  getTopicMetaData = () => {
    axios
      .get(
        `${urls.BACKEND_URL}/api/thread/getThreadMetaData/${this.props.topicObj}/`
      )
      .then((res) => {
        console.log("inside get topicmetada ");
        console.log(res);
      });
  };

  componentDidMount() {
    this.fetchTopics(this.props.forumId);
  }

  onShowMoreButtonClick = () => {
    let numOfItemsShown = this.state.numOfItemsShown + 5;
    if (Object.keys(this.state.topicList).length <= numOfItemsShown)
      this.setState({ showMoreButton: false });
    this.setState({ numOfItemsShown: numOfItemsShown });
  };

  itemsToShow = (numOfItemsShown) => {
    let topicItems = Object.keys(this.state.topicList).map((oneKey) => (
      <TopicCategory
        topicObj={this.state.topicList[oneKey]}
        title={this.state.topicList[oneKey].name}
        userName={this.state.topicList[oneKey].username}
        postedTime={this.state.topicList[oneKey].last_activity}
        lastUser="..."
        topicId={this.state.topicList[oneKey].id}
        slug={this.props.slug}
        setTopic={this.props.setTopic}
        getPosts={this.props.getPosts}
      />
    ));
    return topicItems.slice(0, numOfItemsShown);
  };

  render() {
    return (
      <TopicContainer
        title={this.props.forumName}
        onShowMoreButtonClick={this.onShowMoreButtonClick}
        showMoreButton={this.state.showMoreButton}
      >
        {this.itemsToShow(this.state.numOfItemsShown)}
      </TopicContainer>
    );
  }
}

export default TopicCategories;
