import React from "react";
import { Table } from "react-bootstrap";
import createSurfChartObject from "./SurfChartObject";

class SurfBoardTable extends React.Component {
  render() {
    var surfChartObject = createSurfChartObject();
    var rows = Object.keys(surfChartObject).map((key) => {
      var values = surfChartObject[key];
      return (
        <tr>
          <td>{key}</td>
          {Object.keys(values).map((skill) => {
            return <td>{values[skill]}</td>;
          })}
        </tr>
      );
    });

    return (
      <div style={{ textAlign: "center", width: "100%" }}>
        <h4>Volume based on Weight and Skill Level</h4>
        <br />
        <Table striped bordered hover size="sm">
          <thead>
            <tr>
              <th>WEIGHT(LBS)</th>
              <th>ADVANCED</th>
              <th>ADVAN / INTER</th>
              <th>INTERMEDIATE</th>
              <th>INTER / BEG</th>
              <th>BEGINNER</th>
            </tr>
          </thead>
          <tbody>{rows}</tbody>
        </Table>
      </div>
    );
  }
}

export default SurfBoardTable;
