var createSurfChartObject = () => {
  // table represents the individuals weight
  // and its corresponding volumes
  // order: Advanced, adv/inter, intermediate, inter/beg, beginner
  var surfChartValues = {
    80: [16.0, 16.8, 20.0, 24.0, 32.0],
    85: [16.61, 17.44, 20.77, 24.92, 33.23],
    90: [17.18, 18.04, 21.48, 25.77, 34.36],
    95: [17.7, 18.59, 22.13, 26.56, 35.41],
    100: [19.09, 20.05, 23.86, 28.64, 38.18],
    105: [19.09, 20.05, 23.86, 28.64, 38.18],
    110: [19.5, 20.48, 24.38, 29.25, 39.0],
    115: [20.39, 21.41, 25.48, 30.58, 40.77],
    120: [20.73, 21.76, 25.91, 31.09, 41.45],
    125: [21.02, 22.07, 26.28, 31.53, 42.05],
    130: [21.27, 22.34, 26.59, 31.91, 42.55],
    135: [22.09, 23.2, 27.61, 33.14, 44.18],
    140: [22.27, 23.39, 27.84, 33.41, 44.55],
    145: [23.07, 24.22, 28.84, 34.6, 46.14],
    150: [23.86, 25.06, 29.83, 35.8, 47.73],
    155: [24.66, 25.89, 30.82, 36.99, 49.32],
    160: [25.45, 26.73, 31.82, 38.18, 50.91],
    165: [26.25, 27.56, 32.81, 39.38, 52.5],
    170: [27.05, 28.4, 33.81, 40.57, 54.09],
    175: [27.84, 29.23, 34.8, 41.76, 55.68],
    180: [28.64, 30.07, 35.8, 42.95, 57.27],
    185: [29.43, 30.9, 36.79, 44.15, 58.86],
    190: [30.23, 31.74, 37.78, 45.34, 60.45],
    195: [31.02, 32.57, 38.78, 46.53, 62.05],
    200: [31.82, 33.41, 39.77, 47.73, 63.64],
    205: [32.61, 34.24, 40.77, 48.92, 65.23],
    210: [33.41, 35.08, 41.76, 50.11, 66.82],
    215: [34.2, 35.91, 42.76, 51.31, 68.41],
    220: [36.0, 37.8, 45.0, 54.0, 72.0],
    225: [36.82, 38.66, 46.02, 55.23, 73.64],
    230: [38.68, 40.62, 48.35, 58.02, 77.36],
    235: [39.52, 41.5, 49.4, 59.28, 79.05],
    240: [41.45, 43.53, 51.82, 62.18, 82.91],
  };

  var skillLevel = [
    "ADVANCED",
    "ADVAN / INTER",
    "INTERMEDIATE",
    "INTER / BEG",
    "BEGINNER",
  ];
  // object stor
  var surfChartObject = {};
  // this for loop matches the experience level of each
  // weight to its recommended volume
  for (var value in surfChartValues) {
    var temp = {};
    var values = surfChartValues[value];
    for (var i = 0; i < skillLevel.length; i++)
      temp[skillLevel[i]] = values[i];
    
    surfChartObject[value] = temp;
  }
  return surfChartObject;
};

export default createSurfChartObject;
