import React from "react";
import Carousel from "./Carousel";
import axios from "axios";
import { Row } from "react-bootstrap";
import News from "./News";

/**
 * MainPage contains all the elements visible in the main page
 */
class MainPage extends React.Component {
  state = { photos: [], news: [] };

  ACCESS_KEY_PHOTO = "hicw6mPSF2LVPi-v3aH2A1N4OakJ0SS18AeI6YjnKGg";
  query_photo = "surfing";
  unsplashURL = `https://api.unsplash.com/photos/random/?client_id=${this.ACCESS_KEY_PHOTO}&query=${this.query_photo}&count=10&orientation=landscape`;

  getPhotos = () => {
    axios
      .get(this.unsplashURL)
      .then((res) => this.setState({ photos: res.data }));
  };

  ACCESS_KEY_NEWS = "ad18b84192cd48a3a2d70650e536d9cd";
  query_news = "world%20surf%20league";
  language_news = "en";
  newsURL = `https://newsapi.org/v2/everything?q=${this.query_news}&language=${this.language_news}&apiKey=${this.ACCESS_KEY_NEWS}`;

  getNews = () => {
    axios.get(this.newsURL).then((res) => this.setState({ news: res.data }));
  };

  componentDidMount() {
    this.getPhotos();
    this.getNews();
  }

  render() {
    return (
      <div
        style={{
          width: "99.2%",
          height: "650px",
        }}
      >
        <Row>
          <Carousel photos={this.state.photos} />
        </Row>
        <Row>
          <h1 style={{ marginLeft: "30px", marginTop: "20px" }}>
            Surfing News
          </h1>
        </Row>
        <Row style={{ margin: "15px" }}>
          {this.state.news.length !== 0 && (
            <News news={this.state.news.articles} />
          )}
        </Row>
      </div>
    );
  }
}

export default MainPage;
