import React from "react";
import WeatherSpecCard from "./WeatherSpecCard";
import Header from "./Header";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
/**
 * WeatherSpecs acts as a container for WeatherSpecCard.js
 */
class WeatherSpecs extends React.Component {
  state = {
    forecastSummary: { swell: "", wind: "", waterTemp: "", airTemp: "" },
  };
/**
 *  Since SurfScopeForecast does not have access for daily forecast data,
 *  ChooseTimeStamp chooses the closest timestamp that is available at
 *  a specific time.
 * @param {*} data 
 * @returns 
 */
  chooseTimeStamp(data) {
    if (data.length < 1) {
      return;
    }

    let currentDate = new Date();
    let lastDate = new Date(data[data.length - 1].timestamp * 1000);
    let lastElem = data[data.length - 1];
    // if the current date is after the most recent available date,
    // the most recent data is returned
    if (currentDate.getDate() > lastDate.getDate()) {
      const lastSwell =
        lastElem.swell.minBreakingHeight +
        "-" +
        lastElem.swell.maxBreakingHeight +
        " " +
        lastElem.swell.unit;

      const lastWind =
        lastElem.wind.speed +
        " " +
        lastElem.wind.unit +
        " " +
        lastElem.wind.compassDirection +
        " " +
        lastElem.wind.direction +
        "° ";

      const lastAirTemp = lastElem.condition.temperature + " °" + lastElem.condition.unit;

      this.setState({
        forecastSummary: {
          swell: lastSwell,
          wind: lastWind,
          airTemp: lastAirTemp,
        },
      });

      return;
    }
    // in the case the current date matches a date for which there is data
    // that data is returnd instead
    for (let i in data) {
      //timeStamp date as Unix
      let timeStampDate = new Date(data[i].timestamp * 1000);

      if (timeStampDate.getDate() >= currentDate.getDate()) {
        let carryOver = (timeStampDate.getDate() - currentDate.getDate()) * 24;

        if (carryOver + timeStampDate.getHours() >= currentDate.getHours()) {
          let chosenTime = data[i];

          const swell =
            chosenTime.swell.minBreakingHeight +
            "-" +
            chosenTime.swell.maxBreakingHeight +
            " " +
            chosenTime.swell.unit;

          const wind =
            chosenTime.wind.speed +
            " " +
            chosenTime.wind.unit +
            " " +
            chosenTime.wind.compassDirection +
            " " +
            chosenTime.wind.direction +
            "° ";

          const airTemp =
            chosenTime.condition.temperature +
            " °" +
            chosenTime.condition.unit;

          this.setState({
            forecastSummary: { swell: swell, wind: wind, airTemp: airTemp },
          });

          break;
        }
      }
    }
  }
 /**
   * componentDidMount ensures that the data gets set in the
   * state once the component is ready
   */
  componentDidMount() {
    this.chooseTimeStamp(this.props.data.data);
  }
 /**
   * componentDidUpdate causes the component to re-render when
   * the data inside props gets updated
   * @param {*} prevProps 
   */
  componentDidUpdate(prevProps) {
    if (this.props.data !== prevProps.data) {
      this.chooseTimeStamp(this.props.data.data);
    }
  }

  render() {
    return (
      <div>
        <Header location={this.props.data.location}style={{ padding: "10px" }} />
        <Container  style={{ padding: "10px" }}>
          <Row className="justify-content-md-center">
            <WeatherSpecCard
              description={this.state.forecastSummary.swell}
              spec="Surf Height"
            />

            <WeatherSpecCard
              description={this.state.forecastSummary.wind}
              spec="Wind"
            />

            <WeatherSpecCard
              description={this.state.forecastSummary.airTemp}
              spec="Air Temperature"
            />
          </Row>
        </Container>
      </div>
    );
  }
}

export default WeatherSpecs;
