import React from "react";
import Image from "react-bootstrap/Image";
import MapTypes from "./MapTypes";
import moment from "moment";
import Button from "react-bootstrap/Button";
import { Row, Col, Container } from "react-bootstrap";
import ListGroup from "react-bootstrap/ListGroup";
/**
 * The class Maps (matched to /maps) is able to display weather maps based on areas
 * the images for the maps are from magicseaweed
 *
 */
class Maps extends React.Component {
  // This variable will hold all the elements that are considered
  // to be necessary for the state
  state = { index: 0, timestamps: [], surfspoturls: [], surf_spot_id: 80 };
  // These three variables will be used to retrieve the current date
  year = moment().year();
  month = moment().month();
  date = moment().date();
  // this list stores the unix timestamps created by the dates
  timestamps = [];

  // Following the magicseaweed standards the date needs to be formatted
  // as a string in the shape of yy/mm//day
  // since magic seaweed requires dates to have two digits, it is necessary
  // to add "0" padding for months 1-9
  zero_padding_month = this.month + 1 >= 10 ? "" : "0";
  zero_padding_date = this.date - 1 >= 10 ? "" : "0";
  surfspot_date =
    this.year +
    this.zero_padding_month +
    (this.month + 1) +
    this.zero_padding_date +
    (this.date - 1) +
    "18";
  // variable that will store the urls of the surf spots
  surfspot_urls = [];
  // these dictionaries match the codes used by magiseaweed in their API calls
  // to variables
  chart_types = { swell: 24, wind: 4, period: 25 };
  surf_spots = {
    "North Pacific": 80,
    California: 12,
    "Central Pacific": 39,
    Hawaii: 38,
    "North East Pacific": 24,
    "North Pacific Hurricane": 79,
    "NW Pacific": 50,
    "Pacific Mexico": 23,
    "Pacific North West": 16,
    "Southern California": 70,
    "US Pacific": 13,
  };
  // This list will store a list of buttons based on the elements
  // stored in surf_spots
  surf_spot_items = Object.keys(this.surf_spots).map((surf_spot) => (
    <ListGroup.Item
      onClick={(event) =>
        this.onClickSurfspot(event, this.surf_spots[surf_spot])
      }
    >
      {surf_spot}
    </ListGroup.Item>
  ));
  /**
   * this function coverts utc dates to unix time
   */
  makeTimestamps() {
    for (var i = 0; i < 6; i++) {
      this.timestamps.push(
        moment
          .utc([this.year, this.month, this.date, (6 + i * 3) % 24, 0, 0, 0])
          .unix()
      );
    }
  }
  /**
   * This function takes the dates and map option and makes a url
   * to request data
   * chart_type determines the waveOrGfs variable
   * Possible options are swell/period/wind
   * @param {*} chart_type
   * @param {*} surf_spot_id
   */
  makeSurfspoturls(chart_type, surf_spot_id) {
    let waveOrGfs =
      chart_type === "swell" || chart_type === "period" ? "wave" : "gfs";
    for (var i = 0; i < 6; i++) {
      var surfspot_url = `https://charts-s3.msw.ms/live/${waveOrGfs}/${this.surfspot_date}/940/${surf_spot_id}-${this.timestamps[i]}-${this.chart_types[chart_type]}.gif`;
      this.surfspot_urls.push(surfspot_url);
    }
  }
  /**
   * onClickForward loads future frames upon user request
   */
  onClickForward = () => {
    if (this.state.index + 1 === this.surfspot_urls.length) {
      this.setState({ index: 0 });
    } else {
      this.setState({ index: this.state.index + 1 });
    }
  };
  /**
   * onClickBack loads previous frames upon user request
   */
  onClickBack = () => {
    if (this.state.index - 1 === -1) {
      this.setState({ index: this.surfspot_urls.length - 1 });
    } else {
      this.setState({ index: this.state.index - 1 });
    }
  };
  /**
   * onClickSurfspot sets the state to cause a re-render and load
   * new data
   * @param {*} event
   * @param {*} spot_id
   */
  onClickSurfspot = (event, spot_id) => {
    event.preventDefault();
    this.surfspot_urls = [];
    this.timestamps = [];
    this.makeTimestamps();
    this.makeSurfspoturls("swell", spot_id);

    this.setState({
      surf_spot_id: spot_id,
      timestamps: this.timestamps,
      surfspoturls: this.surfspot_urls,
    });
  };
  /**
   * onClickTab is trigered when a user switches between wind/swell/period
   * and cause the component to render the new data
   * @param {*} chart_type
   */
  onClickTab = (chart_type) => {
    this.surfspot_urls = [];
    this.timestamps = [];
    this.makeTimestamps();
    this.makeSurfspoturls(chart_type, this.state.surf_spot_id);
    this.setState({
      timestamps: this.timestamps,
      surfspoturls: this.surfspot_urls,
    });
  };

  render() {
    return (
      <div>
        <h3 className="ml-3 mt-3">Surf Charts</h3>
        <hr />
        <Container fluid>
          <Row>
            <Col className="col-xs-12 col-sm-6 col-md-6">
              <ListGroup>{this.surf_spot_items}</ListGroup>
            </Col>
            <Col className="col-xs-12 col-sm-6 col-md-6">
              <MapTypes onSubmit={this.onClickTab} />

              <Image
                className="mr-2 my-2"
                src={this.surfspot_urls[this.state.index]}
                style={{ width: 1000, height: 550 }}
                fluid
              />
              <Row className="justify-content-center">
                <h4>
                  {moment
                    .unix(this.timestamps[this.state.index])
                    .format("YYYY-MM-DD HH:mm")}
                </h4>
              </Row>
              <Row className="justify-content-center">
                <Button
                  variant="primary"
                  onClick={this.onClickBack}
                  size="md"
                  style={{ marginRight: 10, marginTop: 10 }}
                >
                  Previous
                </Button>
                <Button
                  variant="primary"
                  onClick={this.onClickForward}
                  style={{ marginLeft: 10, marginTop: 10 }}
                  size="md"
                >
                  Next
                </Button>
              </Row>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Maps;

