import React from "react";
import NavigationBar from "./NavBar/NavigationBar";
import { Route, Router, Switch, Redirect } from "react-router-dom";
import PrivateRoute from "./NavBar/PrivateRoute";
import Maps from "./Maps/Maps";
import Profile from "./UserProfile/Profile";
import axios from "axios";
import SpotForecast from "./SurfSpotCharts/SpotForecast";
import history from "../history";
import InformationFooter from "./InformationFooter";
import ForumCategories from "./Forum/forum/ForumCategories";
import TopicCategories from "./Forum/topic/TopicCategories";
import NewTopic from "./Forum/topic/NewTopic";
import MainPage from "./MainPage/MainPage";
import ForumTopic from "./Forum/post/ForumTopic";
import { withAuth0 } from "@auth0/auth0-react";
import Questionnaire from "./Questionnaire/Questionnaire";
import FantasyLeague from "./FantasyLeague/FantasyLeague";
import "./App.css";
import * as urls from "../urls";
/**
 * App is the main component of the application.
 * App it is used to send information to subcomponents
 * and to assist with the routing
 */
class App extends React.Component {
  // state contains all the variables that are needed
  // to be sent to other components
  state = {
    data: [],
    location: "",
    slug: "default",
    forumId: 0,
    forumName: "",
    topicId: null,
    topicObj: {},
    posts: {},
    userID: "",
    alreadyLoggedIn: true,
  };
  /**
   * isLoggedIn uses auth0 hooks to determine if the user
   * is aunthenticated, and if it is already logged in.
   * If so, the user data is pulled from the back-end
   * @param {*} isAuthenticated
   */
  isLoggedIn(isAuthenticated) {
    console.log(this.state.alreadyLoggedIn, isAuthenticated);
    if (this.state.alreadyLoggedIn && isAuthenticated) {
      let sub = this.props.auth0.user.sub;
      axios.get(`${urls.BACKEND_URL}/surfingInfo/${sub}/`).then((response) => {
        this.setState({ userID: response.data.id, alreadyLoggedIn: false });
      });
    }
  }
  // findLocationJSON converts the standard names of
  // surf spots, to names used by magicseaweed for its
  // API
  findLocationJSON = {
    "fistral north": "fistral_north_1_forecast.json",
    pipeline: "pipeline_616_forecast.json",
    "snapper rocks": "snapper_rocks_1014_forecast.json",
    trestles: "trestles_291_forecast.json",
  };
  /**
   * onSearchSubmit is used when a user enters a surf
   * spot and then clicks on search.
   * onSearchSubmit loads the forecast for the surf spot
   * @param {*} term
   */
  onSearchSubmit = (term) => {
    const locationJSON = this.findLocationJSON[term];
    axios
      .get(`https://pulkitverma1998.github.io/data/${locationJSON}`)
      .then((response) => {
        this.setState({ data: response.data, location: term });
      });
  };
  /**
   * onForumCategoryClick allows App to send the list of forums
   * to the Forum component
   * @param {*} slug
   * @param {*} forumId
   * @param {*} forumName
   */
  onForumCategoryClick = (slug, forumId, forumName) => {
    this.setState({ slug: slug, forumId: forumId, forumName: forumName });
  };
  /**
   * onTopicCategoryClick allows App to send the list of threads
   * inside a forum to the forum threads
   * @param {*} topicId
   * @param {*} topicObj
   * @param {*} postResponse
   */
  onTopicCategoryClick = (topicId, topicObj, postResponse) => {
    this.setState({ topicId: topicId, topicObj: topicObj, post: postResponse });
  };
  /**
   * getPosts allows App to send the list of posts
   * inside a forum thread
   * @param {*} postResponse
   */
  getPosts = (postResponse) => {
    // this.setState({ posts: {} });
    this.setState({ posts: postResponse });
  };

  render() {
    this.isLoggedIn(this.props.auth0.isAuthenticated);
    return (
      <Router history={history}>
        <NavigationBar onSubmit={this.onSearchSubmit} />
        <Switch>
          <Route path="/forum">
            <ForumCategories
              forumId={this.forumId}
              setForum={this.onForumCategoryClick}
            />
          </Route>
          <PrivateRoute path="/profile" component={Profile} />
          <PrivateRoute
            path={`/topics/${this.state.slug}/${this.state.topicId}`}
          >
            <ForumTopic
              topicObj={this.state.topicObj}
              cardStyle="primary"
              buttonColor="danger"
              slug={this.state.slug}
              posts={this.state.posts}
              userID={this.state.userID}
            />
          </PrivateRoute>
          <Route path={`/topics/${this.state.slug}`}>
            <TopicCategories
              forumName={this.state.forumName}
              forumId={this.state.forumId}
              setTopic={this.onTopicCategoryClick}
              slug={this.state.slug}
              getPosts={this.getPosts}
            />
          </Route>
          <Route path="/new-topic">
            <NewTopic slug={this.state.slug} />
          </Route>
          <Route path="/spot-forecast">
            <SpotForecast {...this.state} />
          </Route>

          <Route path="/maps">
            <Maps data={this.state.data} />
          </Route>
          <Route path="/questionnaire">
            <Questionnaire />
          </Route>
          <Route path="/fantasyleague">
            <FantasyLeague />
          </Route>
          <Route path="/">
            <MainPage />
          </Route>
        </Switch>
        <InformationFooter />
      </Router>
    );
  }
}
export default withAuth0(App);
