import React from "react";
import SecretSpot from "./SecretSpot";
import { Table } from "react-bootstrap";
/**
 * SecretSpots acts as a container for all
 * the individual SecretSpot.js components
 * @param {*} param0 
 * @returns 
 */
const SecretSpots = ({ spots, onDelete, onShow }) => {
  return (
    <Table striped bordered hover>
      <thead>
        <tr>
          <th>Spot name</th>
          <th>Coordinates</th>
          <th>Minimum Swell(ft)</th>
          <th>Maximum Wind (kts)</th>
        </tr>
      </thead>
      <tbody>
        {spots.map((spot) => (
          <SecretSpot
            // key={spot.num}
            // spotNum={spots.spotNum}
            spot={spot}
            onDelete={onDelete}
            onShow={onShow}
          />
        ))}
      </tbody>
    </Table>
  );
};

export default SecretSpots;
