import React from "react";
import { Card, Table } from "react-bootstrap";
import { Link } from "react-router-dom";
/**
 * WetsuitQuestionnaireResult is a table containing the
 * results of the wetsuits questionnaire as well as a
 * link to the recommended wetsuits
 */
class WetsuitQuestionnaireResult extends React.Component {
  state = { wetSuitResults: {}, wetSuitUrl: "" };
  componentDidMount() {
    this.setState({
      wetSuitResults: this.props.wetSuitResults.wetSuitResults,
      wetSuitUrl: this.props.wetSuitResults.wetSuitUrl,
    });
  }
  render() {
    // console.log(this.state.wetSuitResults);
    return (
      <Card>
        <Card.Header>
          <h4 className="text-center" style={{ margin: " 0px" }}>
            Wetsuit Results
          </h4>
        </Card.Header>
        <Card.Body>
          <Table striped bordered>
            <tbody>
              <tr>
                <th>Gender</th>
                <td>{this.state.wetSuitResults.gender}</td>
              </tr>
              <tr>
                <th>Size</th>
                <td>{this.state.wetSuitResults.size}</td>
              </tr>
              <tr>
                <th>For Water Temperature</th>
                <td>{this.state.wetSuitResults.waterTemp}</td>
              </tr>
              <tr>
                <th>Your Cold Sensitivity</th>
                <td>{this.state.wetSuitResults.coldness}</td>
              </tr>
              <tr>
                <th>Zipper Type</th>
                <td>{this.state.wetSuitResults.zipper}</td>
              </tr>
            </tbody>
          </Table>
          <a
            rel="noopener noreferrer"
            target="_blank"
            href={this.state.wetSuitUrl}
            className="btn btn-primary"
          >
            Click to view WetSuit
          </a>
        </Card.Body>
      </Card>
    );
  }
}
export default WetsuitQuestionnaireResult;
