import React, { Component } from "react";
import { withAuth0 } from "@auth0/auth0-react";
import axios from "axios";
import SurferCard from "../FantasyLeague/SurferCard";
import { Card, Button, Col } from "react-bootstrap";
import * as urls from "../../urls";
/**
 * FantasyLeagueTeam.js inside of the UserProfile,
 * allows the user to load their fantasy team if
 * they have created any
 */
export class FantasyLeagueTeam extends Component {
  // state stores data from requests to be displayed
  state = { surferTeam: [], showSurfers: true };

  /**
   * deleteTeam allows the user to delete their team
   * from the database
   */
  deleteTeam = () => {
    axios
      .put(
        `${urls.BACKEND_URL}/userfantasyleague/${this.props.auth0.user.sub}/`,
        {
          selectedTeam: [],
          totalScore: 0,
        }
      )
      .then((res) => {
        this.setState({ surferTeam: [] });
      })
      .catch((error) => {
        console.log(error);
      });
  };
  /**
   * componentDidMount ensures that the data gets set in the
   * state once the component is ready
   */
  componentDidMount() {
    axios
      .get(
        `${urls.BACKEND_URL}/userfantasyleague/${this.props.auth0.user.sub}/`
      )
      .then((res) => {
        let result = res.data;
        this.setState({ surferTeam: result });
      })
      .catch((error) => {
        console.log(error);
      });
  }
  //  /**
  //  * componentDidUpdate causes the component to re-render when
  //  * the data inside props gets updated
  //  * @param {*} prevProps
  //  */
  // componentDidUpdate(prevProps) {
  //   if (this.props.data !== prevProps.data) this.getSurferTeam();
  // }

  render() {
    return (
      <Card style={{ width: "27%" }}>
        <Card.Header>
          Your Fantasy League Team
          {this.state.surferTeam.length > 0 && (
            <Button
              onClick={this.deleteTeam}
              variant="danger"
              style={{ float: "right" }}
            >
              Delete
            </Button>
          )}
        </Card.Header>
        <Card.Body>
          {this.state.surferTeam.length > 0 ? (
            <>
              {this.state.surferTeam.length > 0 &&
                this.state.surferTeam.map((surfer) => {
                  return <SurferCard showScore={true} surfer={surfer} />;
                })}
            </>
          ) : (
            "You have not selected a team yet!"
          )}
        </Card.Body>
      </Card>
    );
  }
}

export default withAuth0(FantasyLeagueTeam);
