import React from "react";
import { Card, Row, Button, Col, Container, Modal } from "react-bootstrap";
import "./FantasyLeagueStyle.css";
import goldMedal from "./Medals/GoldMedal.png";
import silverMedal from "./Medals/SilverMedal.png";
import bronzeMedal from "./Medals/BronzeMedal.png";
import TeamContainer from "./TeamContainer";
import TeamSelector from "./SurferSelector/TeamSelector";
import axios from "axios";
import { withAuth0 } from "@auth0/auth0-react";
import * as urls from "../../urls";

class FantasyLeague extends React.Component {
  state = { rankingComponent: true, topFive: [], show: false, counter: 0 };
  getTopFive = () => {
    axios
      .get(`${urls.BACKEND_URL}/getTopFive/`)
      .then((res) => {
        this.setState({ topFive: res.data, counter: this.state.counter + 1 });
      })
      .catch((error) => {
        console.log(error);
      });
  };

  showRanking = () => {
    this.setState({ rankingComponent: true });
  };

  setRanking = () => {
    let { isAuthenticated } = this.props.auth0;
    if (isAuthenticated) {
      this.setState({ rankingComponent: false });
    } else {
      this.setState({ show: true });
    }
  };

  componentDidMount() {
    this.getTopFive();
  }

  componentDidUpdate(prevState) {
    if (this.state.topFive !== prevState.topFive) {
      this.getTopFive();
    }
  }
  render() {
    let surferTeamContainerList = this.state.topFive.map((team) => (
      <TeamContainer showScore={true} surfingInfo={team} />
    ));

    if (this.state.topFive.length === 0) {
      return (
        <div class="loader-container">
          <div class="center">
            <p>Fantasy League ranking is loading...</p>
            <div class="loader"></div>
          </div>
        </div>
      );
    }

    return (
      <div>
        <Modal
          size="sm"
          show={this.state.show}
          onHide={() => this.setState({ show: false })}
          aria-labelledby="example-modal-sizes-title-sm"
        >
          <Modal.Header closeButton>
            <Modal.Title id="example-modal-sizes-title-sm">
              Please Signup/Login to create a Fantasy League Team
            </Modal.Title>
          </Modal.Header>
        </Modal>
        {this.state.rankingComponent ? (
          <div>
            <div className="row justify-content-center">
              <Button style={{ marginTop: "20px" }} onClick={this.setRanking}>
                Create Team
              </Button>
            </div>
            <Row className="podiumDiv">
              <Card className="podium_2">
                <Card.Img
                  variant="top"
                  src={silverMedal}
                  className="medalImg"
                />
                <Card.Body>
                  <Card.Title>2nd Place</Card.Title>
                  <Card.Text>
                    {this.state.topFive[1].username} <br />
                    Score: {this.state.topFive[1].totalTeamScore}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card className="podium_1">
                <Card.Img
                  variant="top"
                  src={goldMedal}
                  className="medalImgGold"
                />
                <Card.Body>
                  <Card.Title>1st Place</Card.Title>
                  <Card.Text>
                    {this.state.topFive[0].username} <br />
                    Score: {this.state.topFive[0].totalTeamScore}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card className="podium_3">
                <Card.Img
                  variant="top"
                  src={bronzeMedal}
                  className="medalImg"
                />
                <Card.Body>
                  <Card.Title>3rd Place</Card.Title>
                  <Card.Text>
                    {this.state.topFive[2].username} <br />
                    Score: {this.state.topFive[2].totalTeamScore}
                  </Card.Text>
                </Card.Body>
              </Card>
            </Row>
            <br></br>
            <Container className="fluid">
              <Row className="justify-content-md-center">
                <Col lg="auto">{surferTeamContainerList[0]}</Col>
                <Col lg="auto">{surferTeamContainerList[1]}</Col>
              </Row>
              <Row className="justify-content-md-center">
                <Col lg="auto">{surferTeamContainerList[2]}</Col>
                <Col lg="auto">{surferTeamContainerList[3]}</Col>
              </Row>
              <Row className="justify-content-lg-center">
                <Col lg="auto">{surferTeamContainerList[4]}</Col>
              </Row>
            </Container>
            <br />
          </div>
        ) : (
          <TeamSelector
            topFive={this.getTopFive}
            showRanking={this.showRanking}
          />
        )}
      </div>
    );
  }
}

export default withAuth0(FantasyLeague);
