import React from "react";
import styled from "styled-components";
import Surfer from "./Surfer";
import { Droppable } from "react-beautiful-dnd";
import { Alert, Button } from "react-bootstrap";
import { withAuth0 } from "@auth0/auth0-react";
import axios from "axios";
import * as urls from "../../../urls";

const Container = styled.div`
  margin: 8px;
  border-radius: 5px;
  border: 1px solid lightgrey;
  width: 24rem;
  padding: 10px;
  height: 510px;
  overflow-y: auto;
`;
const Title = styled.h3`
  font-size: 20px;
  padding: 8px;
`;
const SurferList = styled.div`
  padding: 8px;
`;

export class Column extends React.Component {
  calculateTeamScore = () => {
    var totalScore = 0;
    this.props.selectedSurfersList.forEach(
      (surfer) => (totalScore += parseInt(surfer.score.replace(/\,/g, "")))
    );
    return totalScore;
  };
  onSubmit = () => {
    this.props.showRanking(true);
    var totalScore = this.calculateTeamScore();
    var teamData = {
      totalScore: totalScore,
      selectedTeam: this.props.selectedSurfersList,
    };
    axios
      .put(
        `${urls.BACKEND_URL}/userfantasyleague/${this.props.auth0.user.sub}/`,
        teamData
      )
      .then((response) => {
        this.props.topFive();
      });
  };

  render() {
    return (
      <Container>
        <Title>{this.props.column.title}</Title>
        <Droppable
          isDropDisabled={
            this.props.column.id === "column-2" ? this.props.isUnlocked : false
          }
          droppableId={this.props.column.id}
        >
          {(provided) => (
            <SurferList ref={provided.innerRef} {...provided.droppableProps}>
              {this.props.surfers.map((surfer, index) => (
                <Surfer key={surfer.id} surfer={surfer} index={index} />
              ))}
              {provided.placeholder}
            </SurferList>
          )}
        </Droppable>

        {this.props.column.id === "column-2" && this.props.isUnlocked && (
          <div>
            <Alert variant="success">
              Your team looks complete. You are able to choose up to four
              athletes. You can swap your selection before submitting.
            </Alert>

            <Button
              style={{ width: "100%" }}
              variant="primary"
              size="lg"
              onClick={this.onSubmit}
            >
              Submit!
            </Button>
          </div>
        )}
      </Container>
    );
  }
}

export default withAuth0(Column);
