import React from "react";
import { Nav, NavDropdown, Row, Col, Container } from "react-bootstrap";
import "./Profile.css";
import { Link } from "react-router-dom";
/**
 * 
 * Sidebar is a component that allows the user inside of Profile
 * to switch between different profile sections
 */
class Sidebar extends React.Component {
  render() {
    return (
      <div id="side-bar">
        <Nav className="mr-auto">
          <Col>
            <Row sm>
              <Nav.Link>
                <Link to="/Settings">Settings</Link>
              </Nav.Link>
            </Row>
            <Row sm>
              <Nav.Link>
                <Link to="/SecretSurfSpot">Secret Surf Spots</Link>
              </Nav.Link>
            </Row>
            <Row sm>
              <Nav.Link>
                <Link to="/FantasyLeagueTeam">Fantasy League Team</Link>
              </Nav.Link>
            </Row>
            <Row sm>
              <Nav.Link>
                <Link to="/Questionnaire">Questionnaire Results</Link>
              </Nav.Link>
            </Row>
          </Col>
        </Nav>
      </div>
    );
  }
}
export default Sidebar;
