import React from "react";
import WeatherDays from "./WeatherDays";
import { Card, Container } from "react-bootstrap";
/**
 * Weather acts as a container for the weather forecast component
 * WeatherDays.js
 */
class Weather extends React.Component {
  state = {
    sData: [],
  };
 /**
   * componentDidMount ensures that the data gets set in the
   * state once the component is ready
   */
  componentDidMount() {
    this.getForecast(this.props.data);
  }
  /**
   * componentDidUpdate causes the component to re-render when
   * the data inside props gets updated
   * @param {*} prevProps 
   */
  componentDidUpdate(prevProps) {
    if (this.props.data !== prevProps.data) {
      this.getForecast(this.props.data);
    }
  }
  /**
   * getForecast parses through the data from props and grabs
   * every other element to give enough information without
   * causing an information overflow for the user
   * @param {*} data 
   */
  getForecast = (data) => {
    let weatherList = [];
    for (let indx in data) if (indx % 2 === 0) weatherList.push(data[indx]);

    this.setState({ sData: weatherList });
  };

  render() {
    return (
      <div>
        <Card style={{ marginTop: "30px" }}>
          <Card.Body>
            <Card.Title style={{ textAlign: "left" }}>Weather (°C)</Card.Title>
            <Container fluid>
              <WeatherDays sData={this.state.sData} />
            </Container>
          </Card.Body>
        </Card>
      </div>
    );
  }
}
export default Weather;
