import React, { Component } from "react";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Button from "react-bootstrap/Button";
import "./TopicCategory.css";
import TopicContainer from "./TopicContainer";
import { Editor } from "react-draft-wysiwyg";
import { EditorState, convertToRaw } from "draft-js";
import { withAuth0 } from "@auth0/auth0-react";
import axios from "axios";
import "../../../../node_modules/react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import history from "../../../history";
import * as urls from "../../../urls";

class NewTopic extends Component {
  state = {
    contentState: EditorState.createEmpty(),
    title: "",
  };

  onEditorStateChange = (contentState) => {
    this.setState({
      contentState: contentState,
    });
  };

  static propTypes = {};

  setUser = () => {
    const { user } = this.props.auth0;
    return user;
  };

  onSubmitTopic = (event) => {
    event.preventDefault();
    let topicContentObj = {
      name: this.state.title,
      forum: this.props.slug,
      content: JSON.stringify(
        convertToRaw(this.state.contentState.getCurrentContent())
      ),
      creator: this.setUser().sub,
    };
    console.log(convertToRaw(this.state.contentState.getCurrentContent()));
    axios
      .post(`${urls.BACKEND_URL}/api/thread/create/`, topicContentObj)
      .then((response) => {
        history.push(`/topics/${this.props.slug}`);
      });
  };

  render() {
    return (
      <TopicContainer title="New Thread" showMoreButton={false}>
        <InputGroup className="mb-3">
          <FormControl
            onChange={(e) => this.setState({ title: e.target.value })}
            placeholder="Enter a title..."
            aria-label="Enter a title..."
            aria-describedby="basic-addon1"
          />
        </InputGroup>
        <div className="place-holder">
          <Editor
            wrapperClassName="wrapper-class"
            editorClassName="editor-class"
            toolbarClassName="toolbar-class"
            onEditorStateChange={this.onEditorStateChange}
          />
        </div>
        <div className="topic-button">
          <Button
            onClick={this.onSubmitTopic}
            className="forum-buttom"
            variant="primary"
          >
            Submit
          </Button>
        </div>
      </TopicContainer>
    );
  }
}

export default withAuth0(NewTopic);
