import React from "react";
import { FaTimes } from "react-icons/fa";
/**
 * SecretSpot acts as a container
 * for every existing surfspot.
 * SecretSpot also allows the user
 * to delete a secret surf spot from 
 * the list
 * @param {*} param0 
 * @returns 
 */
const SecretSpot = ({ spot, onDelete, onShow }) => {
  return (
    Object.keys(spot).length > 0 && (
      <tr>
        <td>
          {onShow && (
            <FaTimes
              style={{
                float: "left",
                color: "red",
                cursor: "pointer",
                marginTop: "6px",
              }}
              onClick={() => onDelete(spot.spotName)}
            />
          )}
          {spot.spotName}
        </td>
        <td>
          ({spot.coordinates.x} ,{spot.coordinates.y} )
        </td>
        <td>{spot.minSwell} ft</td>
        <td>{spot.maxWind} kts</td>
      </tr>
    )
  );
};

export default SecretSpot;
