import React from "react";
import { Container, Button, Row, Col, Card, Modal } from "react-bootstrap";
import "./PostCategory.css";
import PostList from "./PostList";
import NewPost from "./NewPost";
import { withAuth0 } from "@auth0/auth0-react";

export class PostContainer extends React.Component {
  state = { showEditor: false, posts: {}, flagPost: true, showModal: false };
  hideEditor = (hideEditor) => {
    this.setState({ showEditor: hideEditor });
  };

  getPosts = (postResponse, flagPost) => {
    this.setState({ posts: postResponse, flagPost: flagPost });
  };

  onClickNewPost = () => {
    let { isAuthenticated } = this.props.auth0;
    if (isAuthenticated) {
      this.setState({ showEditor: true });
      window.scrollTo(0, document.body.scrollHeight);
    } else {
      this.setState({ showModal: true });
    }
  };

  render() {
    return (
      <div>
        <Container className="forum">
          <Row>
            <Col>
              <Card>
                <Card.Header>
                  {this.props.title}
                  <Button
                    className="topic-button"
                    onClick={this.onClickNewPost}
                  >
                    New Post
                  </Button>
                </Card.Header>
                <Card.Body>
                  {this.props.children}
                  <br />
                  {this.state.flagPost ? (
                    <PostList
                      posts={this.props.posts}
                      userID={this.props.userID}
                    />
                  ) : (
                    <PostList
                      posts={this.state.posts}
                      userID={this.props.userID}
                    />
                  )}
                  <br />

                  {this.props.auth0.isAuthenticated &&
                    this.state.showEditor && (
                      <NewPost
                        threadID={this.props.threadID}
                        slug={this.props.slug}
                        hideEditor={this.hideEditor}
                        getPosts={this.getPosts}
                      />
                    )}
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Modal
            show={this.state.showModal}
            onHide={() => this.setState({ showModal: false })}
          >
            <Modal.Header closeButton>
              <Modal.Title>
                Please Signup/Login to create a new post
              </Modal.Title>
            </Modal.Header>
          </Modal>
        </Container>
      </div>
    );
  }
}

export default withAuth0(PostContainer);
