import React, { Component } from "react";
import { Card } from "react-bootstrap";
import SurferCard from "./SurferCard";

export class TeamContainer extends Component {
  render() {
    return (
      <Card style={{ margin: "10px", width: "auto" }}>
        <Card.Header>
          <b>{this.props.surfingInfo.username}</b>
          <div style={{ float: "right" }}>
            {" Team Score: "}
            {this.props.surfingInfo.totalTeamScore}
          </div>
        </Card.Header>
        <Card.Body>
          {this.props.surfingInfo.fantasyLeague.map((surfer) => {
            return <SurferCard showScore={true} surfer={surfer} />;
          })}
        </Card.Body>
      </Card>
    );
  }
}

export default TeamContainer;
