import React from "react";
import { Card, Table } from "react-bootstrap";
/**
 * SurfboardQuestionnaireResult is a table
 * containing the results from SurfBoardQuestionnarie
 */
class SurfboardQuestionnaireResult extends React.Component {
  // state contains the variables received from props
  state = { surfboardResults: {}, surfboardUrl: "" };
  /**
   * ComponentDidMount was done to prvent the Table to appear
   * without content
   */
  componentDidMount() {
    this.setState({
      surfboardResults: this.props.surfboardResults.surfboardResults,
      surfboardUrl: this.props.surfboardResults.surfboardUrl,
    });
  }

  render() {
    return (
      <Card>
        <Card.Header>
          <h4 className="text-center" style={{ margin: " 0px" }}>
            Surfboard Results
          </h4>
        </Card.Header>
        <Card.Body>
          <Table striped bordered>
            <tbody>
              <tr>
                <th>Weight</th>
                <td>{this.state.surfboardResults.weight} lb</td>
              </tr>
              <tr>
                <th>Height</th>
                <td>
                  {this.state.surfboardResults.feet} ft{" "}
                  {this.state.surfboardResults.inches} in
                </td>
              </tr>
              <tr>
                <th>Your Surfboard Size</th>
                <td>{this.state.surfboardResults.surfboardSize}</td>
              </tr>
              <tr>
                <th>Your surfing level</th>
                <td>{this.state.surfboardResults.expLevel}</td>
              </tr>
              <tr>
                <th>For wave size</th>
                <td>{this.state.surfboardResults.waveHeight}</td>
              </tr>
            </tbody>
          </Table>
          <a
            rel="noopener noreferrer"
            target="_blank"
            href={this.state.surfboardUrl}
            className="btn btn-primary"
          >
            Click to view Surfboard
          </a>
        </Card.Body>
      </Card>
    );
  }
}
export default SurfboardQuestionnaireResult;
