import React, { Component } from "react";
import { CardColumns } from "react-bootstrap";
import NewsItem from "./NewsItem";

export default class News extends Component {
  render() {
    return (
      <div>
        <CardColumns>
          {this.props.news.map((news) => (
            <NewsItem news={news} />
          ))}
        </CardColumns>
      </div>
    );
  }
}
