import React, { Component } from "react";
import { Card } from "react-bootstrap";
import "./ImageBlur.css";
import moment from "moment";

export default class NewsItems extends Component {
  render() {
    return (
      <a target="_blank" href={this.props.news.url}>
        <Card className="bg-dark text-white">
          <div className="darken">
            <Card.Img src={this.props.news.urlToImage} alt="Card image" />
          </div>
          <Card.ImgOverlay className="darken">
            <Card.Title>{this.props.news.title}</Card.Title>
            <Card.Text>{this.props.news.description}</Card.Text>
            <Card.Text>
              {moment(new Date(this.props.news.publishedAt)).fromNow()}
            </Card.Text>
          </Card.ImgOverlay>
        </Card>
      </a>
    );
  }
}
