import React from "react";
import PropTypes from "prop-types";
import { Card } from "react-bootstrap";
import "./FantasyLeagueStyle.css";
import { Draggable } from "react-beautiful-dnd";
import styled from "styled-components";
import SurferCard from "../SurferCard";

const Container = styled.div`
  margin: 8px;
  border: 1px solid lightgrey;
  border-radius: 2px;
`;

export class Surfer extends React.Component {
  render() {
    return (
      <Draggable draggableId={this.props.surfer.id} index={this.props.index}>
        {(provided) => (
          <div
            {...provided.draggableProps}
            {...provided.dragHandleProps}
            ref={provided.innerRef}
          >
            <SurferCard
              showScore={false}
              key={this.props.ley}
              surfer={this.props.surfer}
              index={this.props.index}
            />
            {/* {this.props.surfer.name} */}
          </div>
        )}
      </Draggable>
    );
  }
}

export default Surfer;
