import React from "react";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
/**
 * Header acts as a header on top of a surfspot
 * it contains the surfspot name and a button to add
 * it to favorites
 */
class Header extends React.Component {
  render() {
    return (
      <Container fluid  style={{ paddingTop: "5px" }}>
        <Row>
          <Col className="text-left" >
            <h3>
              {this.props.location.charAt(0).toUpperCase() +
                this.props.location.slice(1)}{" "}
              Forecast
            </h3>
          </Col>
          <Col className="text-right">
            <Button variant="outline-primary">Add Favorite</Button>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default Header;
