import React, { Component } from "react";
import { Card, Col, Image } from "react-bootstrap";
import moment from "moment";
/**
 * WeatherDay creates a card for every set of data sent
 * from props (WeatherDays.js)
 */
export default class WeatherDay extends Component {
  render() {
    return (
      <Col style={{ padding: "1px" }}>
        <Card style={{ width: "4.9rem" }}>
          <p style={{ textAlign: "center", justifyContent: "space-between" }}>
            {moment.unix(this.props.sData.timestamp).format("MMMM D")}
            <br />
            {moment.unix(this.props.sData.timestamp).format("ha")}
            <br />
            <Image
              src={`http://cdnimages.magicseaweed.com/30x30/${this.props.sData.condition.weather}.png`}
            />
            <br />
            {this.props.sData.condition.temperature}°
            <br />
          </p>
        </Card>
      </Col>
    );
  }
}
