import React from "react";
import { Card, Table, Col } from "react-bootstrap";
import axios from "axios";
import { withAuth0 } from "@auth0/auth0-react";
import "./Questionnaire.css";
import * as urls from "../../../urls";
/**
 * WetSuitResults contains the user's last results
 * for the Wetsuit part of the questionnaire
 */
class WetSuitResults extends React.Component {
  // state stores data from requests to be displayed
  state = {
    size: "",
    gender: "",
    waterTemp: "",
    coldSensitivity: "",
    zipperType: "",
    wetSuitUrl: "",
    loading: false,
  };
  /**
   * getWetsuitData does an initial request to retrieve
   * the wetsuit results of the user and stores the results
   * in the state
   */
  getWetsuitData() {
    axios
      .get(`${urls.BACKEND_URL}/userwetsuit/${this.props.auth0.user.sub}/`)
      .then((response) => {
        console.log(response);
        this.setState({
          size: response.data.size,
          gender: response.data.gender,
          waterTemp: response.data.waterTemp,
          coldSensitivity: response.data.coldSensitivy,
          zipperType: response.data.zipperType,
          wetSuitUrl: response.data.productUrl,
          loading: true,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  /**
   * componentDidMount ensures that the data gets set in the
   * state once the component is ready
   */
  componentDidMount() {
    this.getWetsuitData();
  }
  render() {
    return (
      <Card>
        <Card.Header>
          <h4 className="text-center" style={{ margin: " 0px" }}>
            Wetsuit Results
          </h4>
        </Card.Header>
        <Card.Body>
          {this.state.loading ? (
            <>
              <Table striped bordered>
                <tbody>
                  <tr>
                    <th>Gender</th>
                    <td>{this.state.gender}</td>
                  </tr>
                  <tr>
                    <th>Size</th>
                    <td>{this.state.size}</td>
                  </tr>
                  <tr>
                    <th>For Water Temperature</th>
                    <td>{this.state.waterTemp}</td>
                  </tr>
                  <tr>
                    <th>Your Cold Sensitivity</th>
                    <td>{this.state.coldSensitivity}</td>
                  </tr>
                  <tr>
                    <th>Zipper Type</th>
                    <td>{this.state.zipperType}</td>
                  </tr>
                </tbody>
              </Table>
              <a
                rel="noopener noreferrer"
                target="_blank"
                href={this.state.wetSuitUrl}
                className="btn btn-primary"
              >
                Click to view WetSuit
              </a>
            </>
          ) : (
            <div class="loader-container">
              <div class="center">
                <div class="loader"></div>
              </div>
            </div>
          )}
        </Card.Body>
      </Card>
    );

    // <Card>
    //   <Card.Header>
    //     <h4 className="text-center" style={{ margin: " 0px" }}>
    //       WetSuit Results
    //     </h4>
    //   </Card.Header>
    //   <Card.Body>
    //     {this.state.size}
    //     <br />
    //     {this.state.gender}
    //     <br />
    //     {this.state.waterTemp}
    //     <br />
    //     {this.state.coldSensitivity}
    //     <br />
    //     {this.state.zipperType}
    //   </Card.Body>
    // </Card>
  }
}

export default withAuth0(WetSuitResults);
