import React from "react";
import ForumCategory from "./ForumCategory";
//import ForumTopicSummary from "../topic/ForumTopicSummary";
import ForumContainer from "./ForumContainer";
import axios from "axios";
import TopicCategories from "../topic/TopicCategories";
import * as urls from "../../../urls";

class ForumCategories extends React.Component {
  state = { forumList: [], loading: true };
  fetchForums = () => {
    axios
      .get(`${urls.BACKEND_URL}/api/forum/`)
      .then((res) => {
        console.log(res);
        this.setState({ forumList: res.data });
        this.setState({ loading: false });
      })
      .catch((error) => {
        console.log(error);
        this.setState({ loading: false });
      });
  };

  componentDidMount() {
    this.fetchForums();
  }

  render() {
    let forumItems = this.state.forumList.map((forum) => (
      <div>
        <ForumCategory
          setForum={this.props.setForum}
          title={forum.name}
          forumDescription={forum.description}
          // previewPost="..."
          previewPostDate={forum.last_activity}
          postsCount={forum.posts_count}
          threadsCount={forum.threads_count}
          forumId={forum.id}
          slug={forum.slug}
        />
      </div>
    ));
    return (
      <ForumContainer title="Categories">
        {this.state.loading ? <div class="loader"></div> : forumItems}
      </ForumContainer>
    );
  }
}
export default ForumCategories;
