import React, { Component } from "react";
import { Card, Container } from "react-bootstrap";
import SwellDays from "./SwellDays";
import moment from "moment";
/**
 * SwellChart acts as a container for the swell forecast components
 *  SwellDays.js
 */
export class SwellChart extends Component {
  state = {
    sData: [],
  };
  /**
   * getForecast grabs every timestamp that is passed in dataPassed,
   * and then groups the timestamps by days
   * @param {*} dataPassed 
   */
  getForecast = (dataPassed) => {
    if (this.props.data.length > 0) {
      let currDate = moment.unix(dataPassed[0].timestamp).format("MM:DD:YYYY");
      const dividedData = [[]];
      let k = 0;
      for (let i = 0; i < dataPassed.length - 1; i++) {
        let aDate = moment.unix(dataPassed[i].timestamp).format("MM:DD:YYYY");
        if (currDate != aDate) {
          currDate = aDate;
          dividedData.push([]);
          k++;
        }
        dividedData[k].push(dataPassed[i]);
        
      }
      this.setState({ sData: dividedData });
    }
  };
  /**
   * componentDidUpdate causes a re-render if the props change
   * @param {*} prevProps 
   */
  componentDidUpdate(prevProps) {
    if (this.props.data !== prevProps.data) {
      this.getForecast(this.props.data);
    }
  }

  render() {
    return (
      <div>
        <Card style={{ marginTop: "30px" }}>
          <Card.Body>
            <Card.Title style={{ textAlign: "left" }}>
              Surf Height (FT)
            </Card.Title>
            <Container fluid>
              <SwellDays sData={this.state.sData} />
            </Container>
          </Card.Body>
        </Card>
      </div>
    );
  }
}

export default SwellChart;
