import React from "react";
import Card from "react-bootstrap/Card";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import { Tooltip, Container, Row, Col } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "./WindComponent.css";
import arrow from "./WindArrow1.jpeg";
import moment from "moment";
/**
 * WindComponent is a component including all the wind-related
 * data for surf spots
 */
class WindComponent extends React.Component {
  // state variable used for storing data from props
  // and displaying it
  state = { data: [] };


  /**
   * getForecast parses through the data from props and grabs
   * every other element to give enough information without
   * causing an information overflow for the user
   * @param {*} data 
   */
  includeInList = (data) => {
    let windList = [];
    for (let indx in data) if (indx % 2 === 0) windList.push(data[indx]);
    this.setState({ data: windList });
  };
 /**
   * componentDidMount ensures that the data gets set in the
   * state once the component is ready
   */
  componentDidMount() {
    this.includeInList(this.props.data);
  }
 /**
   * componentDidUpdate causes the component to re-render when
   * the data inside props gets updated
   * @param {*} prevProps 
   */
  componentDidUpdate(prevProps) {
    if (this.props.data !== prevProps.data) this.includeInList(this.props.data);
  }

  render() {
    //ToolTip containing image of wind chart
    let renderTooltip = (props) => (
      <Tooltip id="button-tooltip">
        Wind direction: {props.wind.direction}°{props.wind.compassDirection}
        <img
          style={{ height: "300px", width: "300px" }}
          src={props.charts.wind}
        />
      </Tooltip>
    );

    // Insert arrow image with a rotation given by the degrees
    let styles = (degrees) => {
      const styles = {
        transform: `rotate(` + degrees + `deg)`,
        height: `14px`,
        width: `10px`,
      };
      return <img src={arrow} style={styles} />;
    };
    /**
     * WindColor matches the intensity past as a parameter
     * to match a certain color based on the intensity
     * @param {*} intensity 
     * @returns 
     */
    let windColor = (intensity) => {
      if (intensity === null) return;
      let color = "";
      if (intensity < 7) color = "green";
      else if (intensity < 12) color = "yellow";
      else color = "red";
      const styles = {
        backgroundColor: color,
        padding: `2px`,
      };
      return (
        <Card.Footer className="text-muted" style={styles}>
          {" "}
        </Card.Footer>
      );
    };
    return (
      <Card style={{ marginTop: "30px" }}>
        <Card.Body>
          <Card.Title style={{ textAlign: "left" }}>Wind (mph)</Card.Title>
          <Container fluid>
            <br />
            <div
              class="profile-container"
              style={{
                width: "100%",
                height: "30%",
                textAlign: "center",
                margin: "auto",

              }}
            >
              <Container fluid>
                <Row>
                  {this.state.data.map(function (ListElement, index) {
                    return (
                      <Col style={{ padding: "1px" }}>
                        <OverlayTrigger
                          key={index}
                          placement="top"
                          overlay={renderTooltip(ListElement)}
                        >
                          <Card className="box" style={{ width: "4.9rem" }}>
                            <table>
                              {/* Row including date */}
                              <tr>
                                <td>
                                  <b>
                                    {moment
                                      .unix(ListElement.timestamp)
                                      .format("MMMM Do")}
                                  </b>{" "}
                                </td>
                              </tr>
                              {/* Row including time*/}
                              <tr>
                                {" "}
                                <td>
                                  {" "}
                                  {moment
                                    .unix(ListElement.timestamp)
                                    .format("ha")}
                                </td>
                              </tr>
                              <br />
                              {/* Row including arrow direction image */}
                              <tr>
                                <td>{styles(ListElement.wind.direction)}</td>
                              </tr>{" "}
                              <br />
                              {/* Row including Wind speed */}
                              <tr>
                                <td>
                                  {" "}
                                  <b>{ListElement.wind.speed}</b>
                                </td>
                              </tr>
                              {/* Row including Wind gusts */}
                              <tr>
                                <td> {ListElement.wind.gusts}</td>
                              </tr>
                              {windColor(
                                (ListElement.wind.speed +
                                  ListElement.wind.gusts) /
                                  2
                              )}
                            </table>
                          </Card>
                        </OverlayTrigger>
                      </Col>
                    );
                  })}
                </Row>
              </Container>
            </div>
          </Container>
        </Card.Body>
      </Card>
    );
  }
}

export default WindComponent;
