import React, { Component } from "react";
import { Card, Col, OverlayTrigger, Tooltip } from "react-bootstrap";
import moment from "moment";
import {
  MainContainer,
  Container,
  BarChartContainer,
  Number,
  MakeBar,
} from "./Bar";
import shortid from "shortid";

/**
 * SwellDay is a component that represents the swell 
 * forecast for every individual day
 */
export default class SwellDay extends Component {
  // the state is used to store and display the data
  // received from props
  state = {
    unit: "ft",
    height: [],
    barData: [],
    date: [],
  };
  /**
   * getRandomKey allows each BarChart Container to have
   * its own independent identifier
   * @returns identifier
   */
  getRandomKey = () => {
    return shortid.generate();
  };

  /**
   * getBarChartData parses through the data coming
   * from props, and stores it in state.barData
   */
  getBarChartData = () => {
    let allAvgs = [];
    let allData = this.props.sData;

    //Loop through API data
    for (let i = 0; i < allData.length; i++) {
      //
      let hour = moment.unix(allData[i].timestamp).format("ha");
      let aAvgSwell = 0;

      let tempObj = {
        time: hour,
        avgSwell: aAvgSwell,
        components: {},
      };

      // Get components some hours have more than others
      let nameComp = [];
      let componentsLength = 0;
      for (let key in allData[i].swell.components) {
        if (allData[i].swell.components[key]) {
          nameComp.push([key, allData[i].swell.components[key]]);
          componentsLength++;
        }
      }

      // stores its elements in TempObj
      for (let j = 0; j < componentsLength; j++) {
        let keyComp = nameComp[j][0];
        let valueComp = nameComp[j][1];
        aAvgSwell += valueComp.height;
        tempObj["components"][keyComp] = valueComp;
      }
      //Calculate avg height
      aAvgSwell = aAvgSwell / componentsLength;

      tempObj.avgSwell = aAvgSwell;
      allAvgs.push(tempObj);
    }

    this.setState({
      barData: allAvgs,
    });
  };
  /**
   * getForecast parses through the data coming
   * from props, and stores it in state's unit,
   * date, and height
   */
  getForecast = () => {
    let allData = this.props.sData;
    let aDate = moment.unix(this.props.sData[0].timestamp);
    this.setState({
      unit: allData[0].swell.unit,
      date: [aDate.format("dddd"), aDate.format("MMMM Do")],
      height: [
        allData[0].swell.minBreakingHeight,
        allData[0].swell.maxBreakingHeight,
      ],
    });
  };
  /**
   * componentDidMount ensures that the data gets set in the
   * state once the component is ready
   */
  componentDidMount() {
    this.getForecast();
    this.getBarChartData();
  }
  /**
   * componentDidUpdate causes the component to re-render when
   * the data inside props gets updated
   * @param {*} prevProps 
   */
  componentDidUpdate(prevProps) {
    if (this.props.sData !== prevProps.sData) {
      this.getForecast();
      this.getBarChartData();
    }
  }

  render() {
    return (
      <Col style={{ padding: "0px" }}>
        <Card style={{ margin: "10px" }}>
          <Card.Body style={{ textAlign: "center" }}>
            <Card.Title style={{ fontSize: "20px" }}>
              {this.state.date[0]}
            </Card.Title>
            <Card.Subtitle className="mb-2 text-muted">
              {this.state.date[1]}
            </Card.Subtitle>
            <Card.Text style={{ fontSize: "17px" }}>
              {this.state.height[0]}-{this.state.height[1]} {this.state.unit}
              <Container>
                <MainContainer>
                  {this.state.barData.map(
                    ({ time, avgSwell, components }, i) => {
                      return (
                        <BarChartContainer key={this.getRandomKey()}>
                          {/* <Number>{avgSwell}</Number> */}
                          <OverlayTrigger
                            overlay={
                              <Tooltip>
                                {Object.keys(components).map((oneKey, i) => {
                                  return (
                                    <div>
                                      <p
                                        style={{
                                          fontSize: "12px",
                                          marginBottom: "0px",
                                          textAlign: "left",
                                        }}
                                      >
                                        {components[oneKey].height}
                                        {this.state.unit}
                                        {" at "}
                                        {components[oneKey].period}
                                        {"s "}
                                        {components[oneKey].compassDirection}
                                      </p>
                                    </div>
                                  );
                                })}
                              </Tooltip>
                            }
                            placement="top"
                          >
                            <MakeBar height={avgSwell * 20} />
                          </OverlayTrigger>
                          <Number>{time}</Number>
                        </BarChartContainer>
                      );
                    }
                  )}
                </MainContainer>
              </Container>
            </Card.Text>
          </Card.Body>
        </Card>
      </Col>
    );
  }
}
