import { Row, Col, Badge } from "react-bootstrap";
import "./ForumCategory.css";
import React, { Component } from "react";
import history from "../../../history";
import draftToHtml from "draftjs-to-html";
import parse from "html-react-parser";
import axios from "axios";
import moment from "moment";
import * as urls from "../../../urls";

export class ForumCategory extends Component {
  state = {
    forumPreview: "",
    timeForumPreview: "",
    usernamePreview: "",
    metaLoading: false,
  };
  fetchMetaData = () => {
    axios
      .get(`${urls.BACKEND_URL}/api/forum/getMetadata/${this.props.forumId}/`)
      .then((res) => {
        console.log(typeof res.data.data);
        if (typeof res.data.data == "string") {
          this.setState({
            forumPreview: res.data.data,
            timeForumPreview: "",
            metaLoading: true,
          });
        } else {
          let contentHTML = JSON.parse(res.data.data.content);
          console.log(contentHTML["blocks"][0]["text"]);
          let contentText = contentHTML["blocks"][0]["text"];
          if (contentText.length > 30) {
            contentText = contentText.substring(0, 30) + "...";
          }
          let postedDate = new Date(res.data.data.created_at);
          console.log(res.data.data.created_at);
          console.log(postedDate);
          this.setState({
            forumPreview: contentText,
            timeForumPreview: moment(postedDate).fromNow(),
            usernamePreview: res.data.data.username,
            metaLoading: true,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        this.setState({ metaLoading: true });
      });
  };
  onForumClick = (e) => {
    e.preventDefault();
    history.push(`/topics/${this.props.slug}`);
    this.props.setForum(this.props.slug, this.props.forumId, this.props.title);
  };
  componentDidMount() {
    this.fetchMetaData();
  }
  render() {
    console.log("forumposts from forumcategroy");
    console.log(this.state.timeForumPreview);
    return (
      <div>
        <Row>
          <Col sm={6}>
            <h3>
              <a
                href={`/topics/${this.props.slug}`}
                onClick={(e) => this.onForumClick(e)}
              >
                {this.props.title}
              </a>
            </h3>
            <p>{this.props.forumDescription}</p>
          </Col>
          <Col sm={2}>
            <Row>
              <Col>
                <span className="forum num threads">
                  {this.props.threadsCount}
                </span>
                <span className="forum num-text">Topics</span>
              </Col>
              <Col>
                <span className="forum num posts">{this.props.postsCount}</span>
                <span className="forum num-text">Posts</span>
              </Col>
            </Row>
          </Col>
          {this.state.metaLoading ? (
            <Col sm={4}>
              <div class="divider"></div>
              <p className="post-preview">{this.state.forumPreview}</p>
              <Badge variant="primary">{this.state.usernamePreview}</Badge>
              <p className="post-preview-date">{this.state.timeForumPreview}</p>
            </Col>
          ) : (
            <Col sm={4}>
              <div class="divider"></div>
              <div class="loader"></div>
            </Col>
          )}
        </Row>
      </div>
    );
  }
}

export default ForumCategory;
